import React from 'react'
import cn from 'classnames'
import makeStyles from "@material-ui/core/styles/makeStyles";
import {fade} from "@material-ui/core";

const useHeaderMenuItem = makeStyles(theme => ({
    menuItem: {
        display: 'inline-flex',
        height: 48,
        alignItems: 'center',
        color: fade(theme.palette.getContrastText(theme.palette.primary.main),0.6),
        textDecoration: 'none',
        fontSize: 13,
        padding: [[theme.spacing(0),theme.spacing(3)]],
        fontWeight: 600,
        fontFamily: 'HK Grotesk SemiBold',
        '&:hover': {
            backgroundColor: fade(theme.palette.getContrastText(theme.palette.primary.main),0.1)
        },
        '&.active': {
            color: theme.palette.getContrastText(theme.palette.primary.main),
            backgroundColor: fade(theme.palette.getContrastText(theme.palette.primary.main),0.1)
            // opacity: 0.4,
        }
    },
}),{name : 'HeaderLink'})

export default function HeaderMenuItem({item,isActive}) {

    const classes = useHeaderMenuItem()

    return <a
        href={item.url || '/link'}
        className={cn(classes.menuItem,'header-menu-item', {'active': isActive})}
    >
        {item.label}
    </a>
}