import React from 'react'
import makeStyles from "@material-ui/core/styles/makeStyles";
import cn from 'classnames'

const useFooterModalStyle = makeStyles(theme => ({
    footerModal: {
        display: 'flex',
        backgroundColor: theme.palette.additional.light,
        margin: [[theme.spacing(0),-theme.spacing(6),-theme.spacing(6),-theme.spacing(6)]],
        padding: [[theme.spacing(4),theme.spacing(4),theme.spacing(6),theme.spacing(4)]],
        borderRadius: [[0,0,15,15]],
        justifyContent: 'flex-end',
        '& button': {
            minWidth: 100
        }
    }
}), { name: 'FooterModal' })

const FooterModal = ({children,className}) => {
    const classes = useFooterModalStyle()
    return (
        <div className={cn(classes.footerModal,className)}>
            {children}
        </div>
    )
}

export default FooterModal