import React from 'react'
import ReactApexChart from 'react-apexcharts'
import {fade} from "@material-ui/core";

export const generateDayWiseTimeSeries = (baseval, count, yrange) => {
    let i = 0;
    let series = [];
    while (i < count) {
        let y = Math.floor(Math.random() * (yrange.max - yrange.min + 1)) + yrange.min;

        series.push([baseval, y]);
        baseval += 86400000;
        i++;
    }
    return series;
}

const defaultOptions = {
    grid: {
        padding: {
            left: 0,
            right: 0
        }
    },

    chart: {
        type: 'area',
        height: 350,
        stacked: false,
        toolbar: {
            show: false
        },
    },
    dataLabels: {
        enabled: false
    },
    stroke: {
        curve: 'smooth'
    },
    fill: {
        type: 'gradient',
        gradient: {
            opacityFrom: 0.3,
            opacityTo: 0,
        }
    },
    legend: {
        showForSingleSeries: true,
        show: true,
        position: 'top',
        horizontalAlign: 'left',
        padding: {
            left: 0,
            right: 0
        }
    },
    yaxis: {
        labels: {
            style: {
                colors: fade('#2C3367',0.4)
            }
        }
    },
    xaxis: {
        type: 'datetime',
        tickPlacement: 'on',
        labels: {
            style: {
                colors: fade('#2C3367',0.6)
            }
        }
    },
};

const defaultSeries = [{
    name: 'Download iOS App',
    data: generateDayWiseTimeSeries(new Date('20 Jan 2020 GMT').getTime(), 5, {
        min: 50,
        max: 250
    })
}];

const AreaChart = ({series = defaultSeries, options = defaultOptions, height="400", version}) => {
    const chartRef = React.createRef();
    React.useEffect(() => {
        if (chartRef && chartRef.current) {
            chartRef.current.chart.toolbar.handleZoomReset()
        }
    }, [version]);


    return (
        <ReactApexChart ref={chartRef} series={series || []} options={options} type="area" height={height}/>
    )
};

export default AreaChart