export default function Header({
	activeMenuItem,
	disableAllControls,
	isShowProgramsMenu
}) {
    const classes = useHeaderStyles();

    const currUser = useCurrentUser();

    const {allAccounts, allProgramGroups, activeProgramGroups, allPrograms, account, programGroup, program} = useNavigationData();
    const navDataReady = !!account && !!programGroup && !!program;

    // Show active program groups, but also show the selected program group if it's not active
    const pgOptions = allProgramGroups.filter(pg => !!activeProgramGroups.find(apg => apg.id === pg.id) || (!!programGroup && programGroup.id === pg.id));

    return (
        <>
            <header className={classes.header}>
                <div className={classes.logoWrapper}>
                    {/*<LogoIcon/>*/}
                    <img src={LogoImage} alt="logo-pic"/>
                </div>
                {disableAllControls ? (
                    <div className={classes.menuItemsBlock}/>
                ) : (
                    <>
                        <div className={classes.menuItemsBlock}>
                            {menuItems.map(item => {
                                return <HeaderMenuItem
                                    key={item.alias}
                                    item={item}
                                    isActive={activeMenuItem === item.label}
                                />
                            })}
                            <DropDown
                                options={menuItems}
                                menuListClass={classes.moreHeaderItemsList}
                                containerClass="more-items-container"
                            >
                                <div className={classes.anchorEl}>
                                    <span>More</span>
                                    <ArrowDownIcon/>
                                </div>
                            </DropDown>
                        </div>

                        <div className={classes.rightHeaderPart}>
                            <div>
                                <HeaderSearchBar/>
                            </div>
                            {navDataReady && (
                                <DropDown
                                    options={allAccounts.filter(a => a.active).map(a => ({
                                        label: a.label,
                                        href: '/account/' + a.id + '/choose',
                                    }))}
                                >
                                    <div className={classes.anchorEl}>
                                        <CampaignIcon/>
                                        <span>{account.label}</span>
                                        <ArrowDownIcon/>
                                    </div>
                                </DropDown>
                            )}
                            <DropDown
                                options={[{
                                    label: 'Logout',
                                    href: '/logout',
                                }]}
                            >
                                <div className={classes.anchorEl}>
                                    {/*<ProfileIcon/>*/}
                                    <Avatar alt="profile-logo" src={AvatarImage} className={classes.avatar}/>
                                    <span>{currUser ? currUser.name : "User"}</span>
                                    <ArrowDownIcon/>
                                </div>
                            </DropDown>
                        </div>

                        {isShowProgramsMenu && navDataReady && (
                            <div className={classes.programsMenu}>
                                <DropDown
                                    options={pgOptions.map(pg => ({
                                        label: pg.label,
                                        href: '/programGroup/' + pg.id + '/choose',
                                    }))}
                                    containerClass="program-dropdown"
                                >
                                    <div className={classes.anchorEl}>
                                        <ProgramIcon/>
                                        <span>{programGroup.label}</span>
                                        <ArrowDownIcon/>
                                    </div>
                                </DropDown>
                                <DropDown
                                    options={allPrograms.filter(p => p.active).map(p => ({
                                        label: p.label,
                                        href: '/program/' + p.id + '/choose',
                                    }))}
                                    containerClass="program-dropdown"
                                >
                                    <div className={classes.anchorEl}>
                                        <ListIcon/>
                                        <span>{program.label}</span>
                                        <ArrowDownIcon/>
                                    </div>
                                </DropDown>
                            </div>
                        )}
                    </>
		    	)}
            </header>
            <Box className={classes.border}/>
        </>
    );
}
Header.propTypes = {
	/** If supplied, a menu item will be treated as "active" if its label matches this string. */
	activeMenuItem: PropTypes.string,
	/** Causes the header to not render any controls. */
	disableAllControls: PropTypes.bool,
	/** Causes the header to show the programs menu.  Ignored if disableAllControls is set. */
	isShowProgramsMenu: PropTypes.bool,
};

