import React, {useContext, useState} from 'react'
import TableRow from "@material-ui/core/TableRow";
import TableCell from '@material-ui/core/TableCell'
import EditableTextField from "../../UI/Form/EditableTextField";
import Link from "@material-ui/core/Link";
import BaseButton from "../../UI/Buttons/BaseButton";
import {ReactComponent as LinkIcon} from '../../../assets/svg/link.svg'
import {ReactComponent as PieChart} from '../../../assets/svg/pirChart.svg'
import {ReactComponent as DeleteIcon} from '../../../assets/svg/delete.svg'
import IconButton from "../../UI/Buttons/IconButton";
import makeStyles from "@material-ui/core/styles/makeStyles";
import cn from 'classnames'
import {useLoadingContext} from "../../Shared/LoaderUtil";
import {updateLink} from "../../Shared/Requests/LinkTrackingRequests";
import {Context} from './context'

const useManageLinkTableRowStyle = makeStyles(theme => ({
  actionCell: {
    '& .icon-button': {
      marginLeft: theme.spacing(1)
    }
  }
}))

const ManageLinkTableRow = ({webLink, openClickReport, openDeleteModal, onCreate, editLink}) => {
  const {programId} = webLink;

  const {isWebLinkNewTemplate} = useContext(Context)
  let initialValues = {
    linkLabel: "",
    originalLink: "",
  };

  const classes = useManageLinkTableRowStyle()
  const [values, setValues] = useState(webLink ? webLink : initialValues);
  const [errors, setErrors] = useState({});
  const [body, setBody] = useState(webLink);
  const [isChanged, setIsChanged] = useState(false);
  const [linkId, setLinkId] = useState();

  React.useEffect(() => {
    setIsChanged(Object.keys(webLink).some(name => {
      return body[name] !== webLink[name]

    }));
  }, [webLink, body]);

  useLoadingContext(() => {
    return updateLink(programId, linkId, body)
        .then(data => {
          setBody(data);
          setValues(data);
          setLinkId(null);
          setIsChanged(false);
          return data;
        })
  }, [linkId], {enabled: !!linkId && !!body});

  const updateField = (data) => {
    const {name, value} = data;
    setBody({...body, [name]: value});
  };

  function submitHandler(event) {
    event.preventDefault()
    setErrors({...errors});

    if (isWebLinkNewTemplate) {
      onCreate(body)
    }
    if (!isWebLinkNewTemplate) {
      setLinkId(webLink.id)
    }
  }

  return (
      <TableRow>
        <TableCell colSpan={2}>
          <EditableTextField
              defaultValue={body.linkLabel}
              name={'linkLabel'}
              onSave={updateField}
          />
        </TableCell>
        <TableCell colSpan={2}>
          <EditableTextField
              defaultValue={body.originalLink}
              name={'originalLink'}
              onSave={updateField}
          />
        </TableCell>
        <TableCell>
          {!isChanged
              ? <Link href="#" underline="none">{values.shortLink}</Link>
              : <BaseButton
                  type="third-main"
                  startIcon={<LinkIcon/>}
                  onClick={submitHandler}
              >
                Generate short link
              </BaseButton>
          }
        </TableCell>
        <TableCell align="right" className={cn(classes.actionCell)}>
          <BaseButton
              type="third-main"
              startIcon={<PieChart/>}
              onClick={openClickReport}
          >
            Click Report
          </BaseButton>
          <IconButton type="lightAccentIcon" size="small" hint="Delete">
            <DeleteIcon
                onClick={openDeleteModal}
            />
          </IconButton>
        </TableCell>
      </TableRow>
  )
}

export default ManageLinkTableRow
