import PropTypes from 'prop-types'
import Button from '@material-ui/core/Button'

const useBaseButtonStyles = makeStyles(theme => ({
    default: {
        height: 36,
        backgroundColor: fade(theme.palette.primary.main, 0.05),
        color: theme.palette.primary.main,
        fontSize: theme.typography.body1.fontSize,
        textTransform: 'none',
        padding: [[theme.spacing(2), theme.spacing(3)]],
        borderRadius: 6,
        fontWeight: 'bold',
        transition: 'all 0.1s',
        '& >span': {
            display: 'inline-flex',
            alignItems: 'center'
        },
        '&:hover': {
            backgroundColor: theme.palette.primary.main,
        }
    },
    success: {
        backgroundColor: theme.palette.success.main,
        color: theme.palette.getContrastText(theme.palette.success.main),
        '& svg': {
            '& path': {
                stroke: fade(theme.palette.getContrastText(theme.palette.secondary.main), 0.6)
            },
        },
        '&:hover': {
            backgroundColor: theme.palette.success.main,
            boxShadow: '0px 2px 6px rgba(21, 28, 86, 0.35);',
            '& svg': {
                '& path': {
                    stroke: fade(theme.palette.getContrastText(theme.palette.secondary.main), 0.6)
                }
            }
        }
    },
    primary: {
        backgroundColor: theme.palette.primary.main,
        color: theme.palette.additional.white,
        '& svg': {
            '& path': {
                stroke: fade(theme.palette.getContrastText(theme.palette.secondary.main), 0.6)
            },
        },

        '&:hover': {
            backgroundColor: '#6247FA',
            boxShadow: '0px 2px 6px rgba(21, 28, 86, 0.35);',
            '& svg': {
                '& path': {
                    stroke: fade(theme.palette.getContrastText(theme.palette.secondary.main), 0.6)
                }
            }
        }
    },
    secondary: {
        backgroundColor: theme.palette.secondary.main,
        color: theme.palette.additional.white,
        '&:hover': {
            backgroundColor: '#6247FA'
        }
    },
    link: {
        backgroundColor: 'transparent',
        color: theme.palette.primary.main,
        '& .MuiButton-label': {
            borderBottom: '1px solid rgba(44, 51, 103, 0.1)',
            width: 'auto',
            transition: 'all 0.5s',
            lineHeight: 1
        },
        '&:hover': {
            backgroundColor: 'transparent',
            '& .MuiButton-label': {
                borderBottom: `1px solid ${theme.palette.primary.main}`,
            }

        }
    },
    error: {
        backgroundColor: 'transparent',
        color: theme.palette.error.main,
        '&:hover': {
            backgroundColor: fade(theme.palette.error.main, 0.07),
        }
    },
    'light-accent': {
        backgroundColor: fade(theme.palette.primary.main, 0.05),
        color: theme.palette.primary.main,
        '& svg': {
            '& path': {
                stroke: theme.palette.primary.main
            }
        },
        '&:hover': {
            backgroundColor: theme.palette.primary.main,
            color: theme.palette.getContrastText(theme.palette.primary.main),
            '& svg': {
                '& path': {
                    stroke: theme.palette.getContrastText(theme.palette.primary.main)
                }
            }
        }
    },
    'light-error': {
        backgroundColor: fade(theme.palette.error.main, 0.07),
        color: theme.palette.error.main,
        '&:hover': {
            backgroundColor: theme.palette.error.main,
            color: theme.palette.additional.white,
            '& path': {
                stroke: theme.palette.additional.white
            }
        },
        '& path': {
            stroke: theme.palette.error.main,
        }
    },
    'third-accent': {
        backgroundColor: 'transparent',
        fontSize: 11,
        color: theme.palette.secondary.main,
        padding: [[theme.spacing(1), theme.spacing(1), theme.spacing(1), theme.spacing(2)]],
        lineHeight: 1,
        height: 26,
        '& .MuiButton-label': {
            alignItems: 'center',
        },
        '& svg': {
            opacity: 0.4
        },
        '& *': {
            stroke: theme.palette.secondary.main
        },
        '&:hover': {
            backgroundColor: theme.palette.secondary.opacity04
        }
    },
    'third-main': {
        backgroundColor: 'transparent',
        color: theme.palette.primary.main,
        height: 26,
        padding: [[theme.spacing(1), theme.spacing(1), theme.spacing(1), theme.spacing(2)]],
        fontSize: 11,
        '& svg': {
            opacity: 1,
            '& *': {
                stroke: theme.palette.primary.main
            }
        },
        '&:hover': {
            backgroundColor: theme.palette.secondary.opacity04
        }
    },
    'third-accent-text': {
        backgroundColor: 'transparent',
        fontSize: 11,
        color: theme.palette.primary.main,
        padding: [[theme.spacing(1), theme.spacing(1), theme.spacing(1), theme.spacing(2)]],
        lineHeight: 1,
        height: 26,
        '& .MuiButton-label': {
            alignItems: 'center',
        },
        '& svg': {
            opacity: 0.4
        },
        '& *': {
            stroke: fade(theme.palette.secondary.main, 0.7),
        },
        '&:hover': {
            backgroundColor: fade(theme.palette.secondary.main, 0.04)
        }
    },
    'third-error': {
        backgroundColor: 'transparent',
        color: theme.palette.error.main,
        padding: [[theme.spacing(1), theme.spacing(1), theme.spacing(1), theme.spacing(2)]],
        lineHeight: 1,
        height: 26,
        '& svg': {
            opacity: 1,
            '& *': {
                stroke: theme.palette.error.main
            }
        },
        '&:hover': {
            backgroundColor: theme.palette.secondary.opacity04
        }
    },
    disabled: {
        '&$primary': {
            backgroundColor: '#E2E2EF',
            color: fade('#7A7FA7', 0.6)
        },
        '&$third-error': {
            opacity: 0.2,
            color: theme.palette.error.main
        },
        '&$third-accent-text': {
            color: fade(theme.palette.secondary.main, 0.3),
            '& path': {
                stroke: fade(theme.palette.secondary.main, 0.3)
            }
        }

    },
    'error-accent': {
        backgroundColor: theme.palette.error.main,
        color: theme.palette.additional.white,
        '&:hover': {
            backgroundColor: theme.palette.error.main,
            opacity: 0.9
        }
    }
}), {name: "BaseButton"});

export default function BaseButton({children, onClick, className, type = 'default', ...restProps}) {
    const classes = useBaseButtonStyles();
    const additionalClass = classes[type];

    return (
        <Button
            onClick={onClick}
            classes={{
                disabled: classes.disabled
            }}
            className={cn(classes.default, additionalClass, className)}

            {...restProps}
        >
            {children}
        </Button>
    );
}
BaseButton.propTypes = {
    onClick: PropTypes.func,
    children: PropTypes.oneOfType([
        PropTypes.element,
        PropTypes.string,
    ]),
    className: PropTypes.string,
    // type: PropTypes.oneOf(['default', 'primary', 'secondary', 'without-background', 'error', 'light-error', 'third-accent', 'error-accent']),
    classes: PropTypes.object,
};
