import React from 'react';
import CustomTheme from "./theming";
import {
	BrowserRouter as Router,
	Switch,
	Route as UnprotectedRoute
} from "react-router-dom";
import Route from "./utilities/routing/ProtectedRoute"
import LoginPage from "./components/Pages/Login/LoginPage";
import {LoginContext} from "./utilities/login/LoginContext";
import HomePage from "./components/Pages/Home/HomePage";
import {PageTitle} from "./components/Shared/PageInfo";
import AccountRouting from "./components/Routing/AccountRouting";
import ProgramGroupRouting from "./components/Routing/ProgramGroupRouting";
import ProgramRouting from "./components/Routing/ProgramRouting";
import {NavigationDataProviderIfAuthenticated} from "./components/Shared/Providers/NavigationDataProvider";
import {AutoLogout} from "./utilities/login/LogoutControl";
import OtherRouting from "./components/Routing/OtherRouting";

const basename = process.env.REACT_APP_BASENAME;

export default function App(props) {
	return (
		<CustomTheme>
			<LoginContext>
				<NavigationDataProviderIfAuthenticated>
					{/*Default page title*/}
					<PageTitle/>

					<Router basename={basename}>
						<Switch>
							<UnprotectedRoute exact path="/login">
								<LoginPage/>
							</UnprotectedRoute>
							<UnprotectedRoute exact path="/logout" component={AutoLogout}/>
							<Route exact path="/">
								<HomePage/>
							</Route>
							<Route path={"/account/:accountId(\\d+)"} render={routeProps => {
								const accountId = parseInt(routeProps.match.params.accountId);
								return <AccountRouting accountId={accountId}/>;
							}}/>
							<Route path={"/programGroup/:programGroupId(\\d+)"} render={routeProps => {
								const programGroupId = parseInt(routeProps.match.params.programGroupId);
								return <ProgramGroupRouting programGroupId={programGroupId}/>;
							}}/>
							<Route path={"/program/:programId(\\d+)"} render={routeProps => {
								const programId = parseInt(routeProps.match.params.programId);
								return <ProgramRouting programId={programId}/>;
							}}/>
							<Route>
								<OtherRouting/>
							</Route>
						</Switch>
					</Router>
				</NavigationDataProviderIfAuthenticated>
			</LoginContext>
		</CustomTheme>
	);
}
