export default function SideBar({menuOptions = [], backButton}) {
	const classes = useSideBarStyles();

	const {account, program} = useNavigationData();

	let BackIcon = <ArrowLeftIcon/>;
	if (backButton && backButton.icon) {
		BackIcon = <Icon component={backButton.icon}/>;
	}

	return (
		<div className={cn(classes.sideBar, 'sidebar')}>
			{backButton && (
				<Box className={cn(classes.backButtonBox)}>
					<BaseButton
						component={Link}
						to={backButton.href}
						className={cn(classes.backButton)}
						startIcon={BackIcon}
					>
						{backButton.label}
					</BaseButton>
				</Box>
			)}

			<div className={cn(classes.menuList)}>
				{menuOptions && menuOptions.length > 0 ? menuOptions.map(item => (
					<SideBarItem
						key={item.label}
						label={item.label}
						href={item.href}
						icon={item.icon}
					/>
				)) : (
					<>
						{program && (
							<ProgramMenuOptions
								program={program}
							/>
						)}
						{account && (
							<AccountMenuOptions
								account={account}
							/>
						)}
					</>
				)}
			</div>

			<div className={cn(classes.menuList, 'additional-list')}>
				<SideBarItem
					label="Help/FAQ"
					href="/help"
					icon={HelpIcon}
				/>
				<SideBarItem
					label="Reporting"
					href="/reporting"
					icon={ReportIcon}
				/>
			</div>
		</div>
	);
}
SideBar.propTypes = {
	menuOptions: PropTypes.array,
	backButton: PropTypes.shape({
		label: PropTypes.string.isRequired,
		href: PropTypes.string.isRequired,
		icon: PropTypes.node,
	}),
}

const useProgramMenuOptionsStyles = makeStyles(theme => ({
	sectionDivider: {
		borderBottom: `1px solid ${fade(theme.palette.getContrastText(bgColor), 0.1)}`,
	},
}), {name: "ProgramMenuOptions"});

function ProgramMenuOptions(props) {
	let {program} = props;
	const classes = useProgramMenuOptionsStyles();

	const currUser = useCurrentUser();
	const [uppCtx] = useCurrentUserProgramPermission(program.id);

	const menuOptions = buildProgramMenuOptions(currUser, program, uppCtx.result);

	return menuOptions.map((item, idx) => {
		let {label, href, icon, badgePromiseFn, endOfSection} = item;
		return (
			<React.Fragment key={idx}>
				<SideBarItem
					key={idx}
					label={label}
					href={href}
					icon={icon}
					badgePromiseFn={badgePromiseFn}
				/>
				{endOfSection && (
					<div className={classes.sectionDivider}/>
				)}
			</React.Fragment>
		);
	});
}
ProgramMenuOptions.propTypes = {
	program: PropTypes.shape({
		id: PropTypes.number.isRequired,
		/** A map of solution IDs to the solution information */
		solutions: PropTypes.object.isRequired,
	}).isRequired,
};

function AccountMenuOptions(props) {
	let {account} = props;

	const currUser = useCurrentUser();

	const menuOptions = buildAccountMenuOptions(currUser, account);
	return menuOptions.map((item, idx) => {
		let {label, href, icon} = item;
		return (
			<SideBarItem
				key={idx}
				label={label}
				href={href}
				icon={icon}
			/>
		);
	});
}
AccountMenuOptions.propTypes = {
	account: PropTypes.shape({
		id: PropTypes.number.isRequired,
	}).isRequired,
};

