import React from 'react'
import ReactApexChart from 'react-apexcharts'

const defaultSeries = [45, 45, 10]
const defaultOptions = {
    chart: {
        type: 'donut',
    },

    stroke: {
        width: 0
    },
    legend: {
        // horizontalAlign: 'center',
        position: 'right',
        itemMargin: {
            vertical: 10
        }
    },
    labels: ['Total Program Messages', 'Total Allotment Messages', 'Allotment'],
    dataLabels: {
        enabled: false
    },
    plotOptions: {
        pie: {
            expandOnClick: false,
            customScale: 0.8
        }
    },
}

const DonutChart = ({series = defaultSeries, options = defaultOptions,height="400"}) => {
    return <ReactApexChart
        series={series}
        options={options}
        type="donut"
        height={height}
    />
}

export default DonutChart