import React from 'react';
import sa from "superagent";
import {CurrentUserProvider} from "../../components/Shared/Providers/CurrentUserProvider";
import LinearProgress from "@material-ui/core/LinearProgress";

const LoginReactContext = React.createContext(null);

export function LoginContext({children}) {
	const [initialized, setInitialized] = React.useState(false);
	const [authenticated, setAuthenticated] = React.useState(false);

	function checkLogin() {
		sa.get('/loginCheck')
			.withCredentials()
			.accept('json')
			.then(r => {
				if(r.body && r.body.login) {
					setAuthenticated(true);
				}
			})
			.catch(e => {
				console.error("could not check login status", e);
			})
			.then(() => setInitialized(true));
	}

	React.useEffect(() => checkLogin(), []);

	return (
		<LoginReactContext.Provider value={{
			authenticated,
			setAuthenticated
		}}>
			<CurrentUserProvider authenticated={authenticated}>
				{!initialized ? (
					<LinearProgress/>
				) : children}
			</CurrentUserProvider>
		</LoginReactContext.Provider>
	);
}

export function useLoginContext() {
	return React.useContext(LoginReactContext);
}
