import React from 'react';
import PropTypes from 'prop-types';
import {Redirect, Switch} from "react-router-dom";
import Route from "../../utilities/routing/ProtectedRoute"
import PageNotFoundMessagePage from "./PageNotFoundMessagePage";
import {useNavigationDataForAccount} from "../Shared/Providers/NavigationDataProvider";

export default function AccountRouting(props) {
	let {accountId} = props;
	useNavigationDataForAccount(accountId);
	return (
		<Switch>
			<Route path="/account/:accountId(\d+)/choose">
				{/*We're just selecting the account and redirecting to the home page*/}
				<Redirect to="/"/>
			</Route>
			<Route>
				<PageNotFoundMessagePage/>
			</Route>
		</Switch>
	);
}
AccountRouting.propTypes = {
	accountId: PropTypes.number.isRequired,
};
