import React from "react";
import {Link} from "react-router-dom";
import PageContainer from "../Pages/PageContainer";
import {useLoginContext} from "../../utilities/login/LoginContext";
import Typography from "@material-ui/core/Typography";

export default function PageNotFoundMessagePage(props) {
	const {authenticated} = useLoginContext();
	return (
		<PageContainer
			pageTitle="Page Not Found"
			disableNavControls={!authenticated}
		>
			<Typography variant="h1" gutterBottom>Oh no!</Typography>
			<Typography variant="body1" paragraph>
				The requested page was not found. Please <Link to="/">return home</Link>.
			</Typography>
		</PageContainer>
	);
}
