export default function Surveys(props) {
	let {programId} = props;
	const [selectedKeyword, setSelectedKeyword] = React.useState(null);
	const [nodeOptionsRefreshId, setNodeOptionsRefreshId] = React.useState(0);

	const [keywordsCtx] = useLoadingContext(
		() => loadNodesByProgramAndHandlerName(programId, "survey")
			.then(nodes => {
				let allKeywords = nodes.map(n => n.keywords)
					.reduce((acc, currVal) => acc.concat(currVal), []);
				allKeywords.sort((k1, k2) => k1.name < k2.name ? -1 : (k1.name > k2.name ? 1 : 0));

				const newKeyword = allKeywords.length > 0 ? allKeywords[0] : null;
				setSelectedKeyword(newKeyword);
				return allKeywords;
			}),
		[]
	);

	const {result: surveyKeywords} = keywordsCtx;

	const [nodeOptionsCtx, setNodeOptionsCtx] = useLoadingContext(
		() => loadNodeOptions(selectedKeyword.nodeId),
		[selectedKeyword, nodeOptionsRefreshId],
		{enabled: !!selectedKeyword}
	);

	function onChangeKeyword(newKeywordId) {
		let newKeyword = surveyKeywords.find(kw => kw.id === newKeywordId);
		setSelectedKeyword(newKeyword);
	}

	function saveNodeOptions(nodeOptionsToSave) {
		if (!selectedKeyword) {
			return Promise.reject("No keyword selected");
		}

		// Mark the loading context as loading first
		setNodeOptionsCtx(prevCtx => ({...prevCtx, loading: true}));

		return updateNodeOptions(selectedKeyword.nodeId, nodeOptionsToSave)
			// Replace the loading context results since we already have the data instead of doing an extra refresh
			.then(newNodeOptions => setNodeOptionsCtx(prevCtx => ({...prevCtx, result: newNodeOptions, loading: false})))
			.catch(reason => setNodeOptionsCtx(prevCtx => ({...prevCtx, loading: false, reason: reason})));
	}

	function refreshNodeOptions() {
		setNodeOptionsRefreshId(prev => prev + 1);
	}

	function createSurveyQuestion(newSequenceNumber) {
		if (!selectedKeyword) {
			return Promise.reject("No keyword selected");
		}
		return createSurveyQuestionForNodeOptionsSurvey(selectedKeyword.nodeId, newSequenceNumber)
			.then(surveyQuestions => {
				// The array from the Promise result only contains the new question, not all the originals, so we
				// shouldn't use those as a replacement. We'll just reload the whole list
				refreshNodeOptions();
			});
	}

	function deleteSurveyQuestion(questionId) {
		if (!selectedKeyword) {
			return Promise.reject("No keyword selected");
		}
		return deleteSurveyQuestionForNodeOptionsSurvey(selectedKeyword.nodeId, questionId)
			.then(refreshNodeOptions);
	}

	return (
		<SurveysWithData
			programId={programId}
			keywordsCtx={keywordsCtx}
			selectedKeyword={selectedKeyword}
			onChangeKeyword={onChangeKeyword}
			nodeOptionsCtx={nodeOptionsCtx}
			createSurveyQuestion={createSurveyQuestion}
			saveNodeOptions={saveNodeOptions}
			deleteSurveyQuestion={deleteSurveyQuestion}
			loadReportDataFn={loadReportData}
		/>
	);
}
Surveys.propTypes = {
	programId: PropTypes.number.isRequired,
};

const useSurveysWithDataStyles = makeStyles(theme => ({
	selectBox: {
		marginTop: theme.spacing(1),
		'& .select-survey': {
			width: 225
		}
	},
	learnMoreButton: {
		marginLeft: theme.spacing(1)
	},
	subTopicWrapper: {
		marginTop: theme.spacing(4),
		display: 'flex',
		alignItems: 'center',
		'& svg': {
			marginLeft: theme.spacing(1),
			'& path': {
				stroke: fade(theme.palette.secondary.main, 0.4)
			}
		}
	},
	surveysResultsIphoneWrapper: {
		display: 'flex',
		marginTop: theme.spacing(2)
	},
}), {name: "SurveysWithData"});

export function SurveysWithData(props) {
	let {programId, keywordsCtx, selectedKeyword, onChangeKeyword, nodeOptionsCtx, createSurveyQuestion, saveNodeOptions, deleteSurveyQuestion, loadReportDataFn} = props;
	const classes = useSurveysWithDataStyles();

	const {result: surveyKeywords, loading: loadingKeywords, reason: keywordError} = keywordsCtx;

	const hasSurveyNodes = !!surveyKeywords && surveyKeywords.length > 0;
	const instructionURL = "https://irio.com/SurveyInstruction.html";

	return (
		<>
			<Typography variant="h1">Surveys</Typography>

			{!!keywordError ? (
				<Alert severity="error">Unable to load some of your data. Please try again later.</Alert>
			) : (
				<>
					{!loadingKeywords && !hasSurveyNodes && (
						<Alert severity="info">No surveys found on this program</Alert>
					)}

					{hasSurveyNodes && (
						<>
							<Box className={cn(classes.selectBox)}>
								<Select
									value={selectedKeyword ? selectedKeyword.id : ""}
									onChange={e => onChangeKeyword(parseInt(e.target.value))}
									IconComponent={ArrowDownIcon}
									className={cn('select-survey')}
								>
									{surveyKeywords.map((surveyKeyword, idx) => (
										<MenuItem key={idx} value={surveyKeyword.id}>{surveyKeyword.name}</MenuItem>
									))}
								</Select>
								<BaseButton
									component="a"
									href={instructionURL}
									target="_blank"
									type="third-accent-text"
									startIcon={<InfoIcon/>}
									className={cn(classes.learnMoreButton)}
								>
									Learn More
								</BaseButton>
							</Box>

							<Box className={cn(classes.subTopicWrapper)}>
								<Typography variant="h2">Survey Flow</Typography>
								{' '}
								<Tooltip
									arrow
									title={(
										<Typography>
											The phone below shows the current survey flow.<br/>
											To make updates to the questions or the order they are being asked, select
											the 3 dots below each message bubble. If you need additional help, feel free
											to contact your account manager!
										</Typography>
									)}
								>
									<span><InfoIcon/></span>
								</Tooltip>
							</Box>
							<Box className={cn(classes.surveysResultsIphoneWrapper)}>
								{selectedKeyword && (
									<SurveyPreview
										selectedKeyword={selectedKeyword}
										nodeOptionsCtx={nodeOptionsCtx}
										createSurveyQuestion={createSurveyQuestion}
										saveNodeOptions={saveNodeOptions}
										deleteSurveyQuestion={deleteSurveyQuestion}
									/>
								)}

								{selectedKeyword && (
									<SurveyReportData
										programId={programId}
										nodeId={selectedKeyword.nodeId}
										loadReportDataFn={loadReportDataFn}
									/>
								)}
							</Box>
						</>
					)}
				</>
			)}
		</>
	);
}
SurveysWithData.propTypes = {
	programId: PropTypes.number.isRequired,
	/** The loading context for the keywords */
	keywordsCtx: PropTypes.object.isRequired,
	/** The selected keyword */
	selectedKeyword: PropTypes.object,
	/** Called with params (keywordId) to change the selected keyword */
	onChangeKeyword: PropTypes.func.isRequired,
	/** The loading context for the node options */
	nodeOptionsCtx: PropTypes.object.isRequired,
	/** Called with params (newSequenceNumber) to create a new survey question at the provided index. Returns a Promise. */
	createSurveyQuestion: PropTypes.func.isRequired,
	/** Called with params (nodeOptionsToSave) to replace/save the node options. Returns a Promise */
	saveNodeOptions: PropTypes.func.isRequired,
	/** Called with params (questionId) to delete a question. Returns a Promise */
	deleteSurveyQuestion: PropTypes.func.isRequired,
	/** Called with params (reportName:string, params:object). Returns a Promise with the report data */
	loadReportDataFn: PropTypes.func.isRequired,
};

const useSurveyReportDataStyles = makeStyles(theme => ({
	paperSurveysResult: {
		padding: [[theme.spacing(2),theme.spacing(4),theme.spacing(4),theme.spacing(4)]],
		maxWidth: 720,
		alignSelf: 'flex-start',
		flex: 1,
	},
	subtitleSurveysResult: {
		display: 'flex',
		justifyContent: 'space-between',
		alignItems: 'center'
	},
}), {name: "SurveyReportData"});

function SurveyReportData(props) {
	let {programId, nodeId, loadReportDataFn} = props;
	const classes = useSurveyReportDataStyles();

	const [pageNum, setPageNum] = React.useState(0);
	const [pageSize, setPageSize] = React.useState(100);

	// TODO do we need to filter by keyword? Web and Excel.

	const reportName = "surveyAnswersReport";

	const [reportDataCtx] = useLoadingContext(
		() => loadReportDataFn(reportName, {
			programId: programId,
			nodeId: nodeId,
			"start-date": null,
			"end-date": null,
			$pageNum: pageNum,
			$pageSize: pageSize,
		}),
		[programId, nodeId, pageNum, pageSize]
	);
	const {result: reportData, loading, reason} = reportDataCtx;

	const uri = "/rest/v1/reports/" + encodeURIComponent(reportName) + "/excel"
		+ "?programId=" + programId
		+ "&nodeId=" + nodeId;

	return (
		<Paper elevation={0} className={cn(classes.paperSurveysResult)}>
			<Box className={cn(classes.subtitleSurveysResult)} mb={2}>
				<Typography variant="h2">Survey Results</Typography>
				<BaseButton
					component="a"
					href={uri}
					target="_blank"
					type="third-main"
					startIcon={<DownLoadIcon/>}
				>
					Export to Excel
				</BaseButton>
			</Box>

			{loading ? (
				<div><CircularProgress/></div>
			) : (!!reason || !reportData) ? (
				<Alert severity="error">Unable to load the results. Please try again later.</Alert>
			) : (
				<ReportDataResults
					reportData={reportData}
					onChangePageNum={setPageNum}
				/>
			)}

		</Paper>
	);
}
SurveyReportData.propTypes = {
	programId: PropTypes.number.isRequired,
	nodeId: PropTypes.number.isRequired,
	/** Called with params (reportName:string, params:object). Returns a Promise with the report data */
	loadReportDataFn: PropTypes.func.isRequired,
};

function SurveyPreview(props) {
	let {selectedKeyword, nodeOptionsCtx, createSurveyQuestion, saveNodeOptions, deleteSurveyQuestion} = props;

	const [editingId, setEditingId] = React.useState(null);
	const [editingFinalQuestion, setEditingFinalQuestion] = React.useState(false);
	const [working, setWorking] = React.useState(false);

	const {result: nodeOptions, loading} = nodeOptionsCtx;

	const sortedQuestions = nodeOptions ? nodeOptions.surveyQuestions : null;
	if (sortedQuestions) {
		sortedQuestions.sort((q1, q2) => q1.sequenceNumber - q2.sequenceNumber);
	}

	function clearEditing() {
		setEditingId(null);
		setEditingFinalQuestion(false);
	}

	function localSaveNodeOptions(nodeOptionsToSave) {
		return saveNodeOptions(nodeOptionsToSave)
			.then(clearEditing);
	}

	function onSaveSurveyQuestion(newText, questionId) {
		if (!newText.trim()) {
			return Promise.reject("Text cannot be empty");
		}

		const newSurveyQuestions = editQuestion(sortedQuestions, questionId, {text: newText});
		return saveQuestions(newSurveyQuestions);
	}

	function moveUp(index) {
		if (index > 0) {
			let curr = sortedQuestions[index];
			let prev = sortedQuestions[index - 1];

			let topSeqNum = prev.sequenceNumber;
			let bottomSeqNum = curr.sequenceNumber;
			let newSurveyQuestions = sortedQuestions;
			newSurveyQuestions = editQuestion(newSurveyQuestions, curr.id, {sequenceNumber: topSeqNum});
			newSurveyQuestions = editQuestion(newSurveyQuestions, prev.id, {sequenceNumber: bottomSeqNum});
			return saveQuestions(newSurveyQuestions);
		} else {
			return Promise.reject("Cannot move the question up any higher");
		}
	}

	function moveDown(index) {
		if (index < sortedQuestions.length - 1) {
			let curr = sortedQuestions[index];
			let next = sortedQuestions[index + 1];

			let topSeqNum = curr.sequenceNumber;
			let bottomSeqNum = next.sequenceNumber;
			let newSurveyQuestions = sortedQuestions;
			newSurveyQuestions = editQuestion(newSurveyQuestions, curr.id, {sequenceNumber: bottomSeqNum});
			newSurveyQuestions = editQuestion(newSurveyQuestions, next.id, {sequenceNumber: topSeqNum});
			return saveQuestions(newSurveyQuestions);
		} else {
			return Promise.reject("Cannot move the question down any lower");
		}
	}

	function saveQuestions(newSurveyQuestions) {
		return localSaveNodeOptions({
			...nodeOptions,
			surveyQuestions: newSurveyQuestions,
		});
	}

	function onDeleteSurveyQuestion(questionId) {
		setWorking(true);
		deleteSurveyQuestion(questionId)
			.then(clearEditing)
			.finally(() => setWorking(false));
	}

	/**
	 * @param newSequenceNumber {number} the new sequence number, where all other questions will be incremented if matching or greater. This can be -1 to put the question at the end
	 */
	function onAddSurveyQuestion(newSequenceNumber) {
		setWorking(true);
		createSurveyQuestion(newSequenceNumber)
			.then(clearEditing)
			.finally(() => setWorking(false));
	}

	function onSaveFinalResponse(newText) {
		if (!newText.trim()) {
			return Promise.reject("Text cannot be empty");
		}

		return localSaveNodeOptions({
			...nodeOptions,
			finalResponse: newText,
		});
	}

	return (
		<LoadingOverlay loading={working || loading}>
			<IPhoneWrapper>
				<MessagesDisplay
					messages={[{inbound: true, text: selectedKeyword.name}]}
					hideMetadata
				/>

				{sortedQuestions && sortedQuestions.map((nodeOptionsSurveyQuestion, idx) => {
					let {id, sequenceNumber, text, attachments} = nodeOptionsSurveyQuestion;
					let isEditing = id === editingId;
					let firstAttachment = attachments && attachments.length > 0 ? attachments[0] : null;
					return (
						<React.Fragment key={id}>
							<MessagesDisplay
								messages={[{text: text, inbound: false, attachment: firstAttachment}]}
								renderMessageText={!isEditing ? null : localText => (
									<TextAreaFieldWithConfirm
										value={localText}
										onSave={value => onSaveSurveyQuestion(value, id)}
										onCancel={() => setEditingId(null)}
									/>
								)}
								renderMessageSupplement={msg => isEditing ? null : (
									<ActionButtons>
										{idx > 0 && (
											<IconButton size="small" onClick={() => moveUp(idx)}>
												<ArrowUpIcon/>
											</IconButton>
										)}
										{idx < sortedQuestions.length - 1 && (
											<IconButton size="small" onClick={() => moveDown(idx)}>
												<ArrowDownIcon/>
											</IconButton>
										)}
										{/*
											We add new questions before the selected value because if there are no questions, we'll
											still at least have a button on the "final question", and the new question will go
											before that.
										*/}
										<IconButton size="small" onClick={() => onAddSurveyQuestion(sequenceNumber)}>
											<PlusIcon/>
										</IconButton>
										<IconButton size="small" onClick={() => setEditingId(id)}>
											<EditIcon/>
										</IconButton>
										<IconButton size="small" onClick={() => onDeleteSurveyQuestion(id)}>
											<DeleteIcon/>
										</IconButton>
									</ActionButtons>
								)}
								hideMetadata
							/>

							<MessagesDisplay
								messages={[{inbound: true, text: "Some Info"}]}
								hideMetadata
							/>
						</React.Fragment>
					);
				})}

				{nodeOptions && nodeOptions.finalResponse && (
					<>
						<MessagesDisplay
							messages={[
								{
									text: nodeOptions.finalResponse,
									inbound: false,
									attachment: nodeOptions.finalResponseAttachmentFile,
								},
							]}
							renderMessageText={!editingFinalQuestion ? null : localText => (
								<TextAreaFieldWithConfirm
									value={localText}
									onSave={value => onSaveFinalResponse(value)}
									onCancel={() => setEditingFinalQuestion(false)}
								/>
							)}
							renderMessageSupplement={msg => editingFinalQuestion ? null : (
								<ActionButtons>
									<IconButton size="small" onClick={() => onAddSurveyQuestion(-1)}>
										<PlusIcon/>
									</IconButton>
									<IconButton size="small" onClick={() => setEditingFinalQuestion(true)}>
										<EditIcon/>
									</IconButton>
								</ActionButtons>
							)}
							hideMetadata
						/>

						<Box pb={8}/>
					</>
				)}
			</IPhoneWrapper>
		</LoadingOverlay>
	);
}
SurveyPreview.propTypes = {
	/** The currently selected keyword */
	selectedKeyword: PropTypes.object.isRequired,
	/** The loading context for the node options */
	nodeOptionsCtx: PropTypes.object.isRequired,
	/** Called with params (newSequenceNumber) to create a new survey question at the provided index. Returns a Promise. */
	createSurveyQuestion: PropTypes.func.isRequired,
	/** Called with params (nodeOptionsToSave) to replace/save the node options. Returns a Promise */
	saveNodeOptions: PropTypes.func.isRequired,
	/** Called with params (questionId) to delete a question. Returns a Promise */
	deleteSurveyQuestion: PropTypes.func.isRequired,
};

function TextAreaFieldWithConfirm(props) {
	const {value, onSave, onCancel} = props;
	const [formValue, setFormValue] = React.useState(value || "");
	return (
		<>
			<Box mb={1} display="flex" justifyContent="flex-end">
				<Paper>
					<TextField
						multiline
						rows={3}
						variant="outlined"
						value={formValue}
						onChange={e => setFormValue(e.target.value)}
					/>
				</Paper>
			</Box>
			<Box display="flex" justifyContent="flex-end">
				<IconButton onClick={onCancel} color="success">
					<CloseIcon/>
				</IconButton>
				<Box ml={2}/>
				<IconButton onClick={() => onSave(formValue)}>
					<CheckIcon/>
				</IconButton>
			</Box>
		</>
	);
}
TextAreaFieldWithConfirm.propTypes = {
	value: PropTypes.string,
	/** Called with params (newText:string) to save the value. Returns a Promise. */
	onSave: PropTypes.func.isRequired,
	/** Called with no params to cancel the edit */
	onCancel: PropTypes.func.isRequired,
};

/**
 * @param allQuestions {[]} the array of questions
 * @param questionId {number} the ID of the question
 * @param alterationObj {object} the alterations to apply to the question object, such as {text: "Hello"}
 * @return {*[]} the new array of questions where the one question was replaced with a copied and altered object
 */
function editQuestion(allQuestions, questionId, alterationObj) {
	const idx = allQuestions.findIndex(sq => sq.id === questionId);
	const origQuestion = allQuestions[idx];
	const newSurveyQuestions = [...allQuestions];
	newSurveyQuestions.splice(idx, 1, {
		...origQuestion,
		...alterationObj,
	});
	return newSurveyQuestions;
}

const useActionButtonsStyles = makeStyles(theme => ({
	actions: {
		display: 'flex',
		justifyContent: 'flex-end',
		marginTop: theme.spacing(2),
		'& .icon-button':{
			backgroundColor: fade('#ffffff',0.07),
			width: 18,
			height: 18,
			borderRadius: 4,
			marginLeft: 5,
			'& svg': {
				width: 10,
				height: 10,
				'& path': {
					stroke: theme.palette.getContrastText(theme.palette.secondary.main)
				}
			}
		}
	}
}), {name: "ActionButtons"});

function ActionButtons(props) {
	let {children} = props;
	const classes = useActionButtonsStyles();
	return (
		<Box className={cn(classes.actions)}>
			{children}
		</Box>
	);
}
ActionButtons.propTypes = {
	/** The icon buttons */
	children: PropTypes.any,
};
