import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import {Link, useRouteMatch} from "react-router-dom";
import Typography from "@material-ui/core/Typography";
import {useLoadingContext} from "../Shared/LoaderUtil";

export function SideBarItem({label, href, icon: Icon, badgePromiseFn}) {
	const [badgeRefreshTime, setBadgeRefreshTime] = React.useState(0);

	const routeMatch = useRouteMatch(href);
	const active = routeMatch && routeMatch.isExact;

	// Update the refresh time every X seconds
	const timerIdRef = React.useRef(-1);
	React.useEffect(() => {
		timerIdRef.current = setInterval(() => setBadgeRefreshTime(Date.now()), 30000);
		return () => clearInterval(timerIdRef.current);
	}, []);

	const [badgeValCtx] = useLoadingContext(badgePromiseFn, [badgeRefreshTime], {enabled: !!badgePromiseFn});
	const {result: count} = badgeValCtx;

	return (
		<Link
			to={href}
			className={cn('menu-item', {'active': !!href && active}, {'topic': !href})}
		>
			{Icon && <Icon/>}
			<Typography variant="body1" className={cn('title')}>{label}</Typography>
			{count && (<span className={cn('count')}>{count}</span>)}
		</Link>
	);
}
SideBarItem.propTypes = {
	label: PropTypes.string.isRequired,
	href: PropTypes.string,
	icon: PropTypes.object,
	/** An optional function to call with no parameters, which returns a Promise with a badge count, string, or other information to display on the link as a badge */
	badgePromiseFn: PropTypes.func,
};
