import React, {useState, useCallback} from 'react'
import PropTypes from 'prop-types'
import cn from 'classnames'
import {Link} from "react-router-dom";
import Menu from '@material-ui/core/Menu';
import MenuItem from "@material-ui/core/MenuItem";
import {action} from "@storybook/addon-actions";
import { makeStyles } from "@material-ui/core/styles";
import ListItemText from "@material-ui/core/ListItemText";

const useDropDrownStyle = makeStyles(theme => ({
	root: {
		display: 'inline-block'
	},
}),{ name : 'DropDown' })

export default function DropDown({children, options = [], containerClass,menuListClass}) {
	const classes = useDropDrownStyle()

	const [anchorEl, setAnchorEl] = useState(false)

	const handleClick = useCallback((e) => {
		setAnchorEl(e.target)
	}, [])

	const handleClose = useCallback(() => {
		setAnchorEl(null)
	}, [])

	return (
		<>
			<div
				className={cn(classes.root,containerClass)}
				onClick={handleClick}
			>
				{children}
			</div>
			<Menu
				open={Boolean(anchorEl)}
				anchorEl={anchorEl}
				onClose={handleClose}
				getContentAnchorEl={null}
				anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
				transformOrigin={{ vertical: "top", horizontal: "center" }}
				keepMounted
				className={cn(menuListClass)}
			>
				{options.length < 1 ? [
					<MenuItem
						key={1}
						disabled
					>
						<ListItemText>No entries</ListItemText>
					</MenuItem>
				] : options.map((option, index) => {
					let {label, href} = option;
					return (
						<MenuItem
							key={`${label}-${index}`}
							component={href ? Link : undefined}
							to={href}
							onClick={() => {
								action('click option');
								handleClose();
							}}
						>
							<ListItemText>{label}</ListItemText>
						</MenuItem>
					);
				})}
			</Menu>
		</>
	);
}

DropDown.propTypes = {
	children: PropTypes.element,
	options: PropTypes.arrayOf(PropTypes.shape({
		label: PropTypes.string.isRequired,
		href: PropTypes.string,
	}))
}
