import React from "react";
import ModalContent from "../UI/Modal/ModalContent";
import HeaderModal from "../UI/Modal/HeaderModal";
import Box from "@material-ui/core/Box";
import cn from 'classnames'
import FooterModal from "../UI/Modal/FooterModal";
import BaseButton from "../UI/Buttons/BaseButton";
import MaterialModal from "../UI/Modal/Modal";
import makeStyles from "@material-ui/core/styles/makeStyles";
import ReportDataResults from "./ReportDataResults";

const useOutgoingMessagesTableStyle = makeStyles(theme => ({
	footerModal: {
		'& button': {
			minWidth: 180
		}
	}
}), {name: 'TablePreviewModal'})

export default function TablePreviewModal({contentClass,open,handleClose,title,reportData}) {
	const classes = useOutgoingMessagesTableStyle()

	return (
		<MaterialModal
			open={open}
		>
			<ModalContent>
				<HeaderModal handleClose={handleClose} title={title}/>
				<Box className={cn(contentClass)} mb={4}>
					{reportData && (<ReportDataResults
						reportData={reportData}
// TODO
onChangePageNum={newPageNum => alert("Page change not yet implemented")}
						dense
					/>)}
				</Box>
				<FooterModal className={cn(classes.footerModal)}>
					<BaseButton type="primary">Add to Dashboard</BaseButton>
				</FooterModal>
			</ModalContent>
		</MaterialModal>
	);
}
