import React from 'react'
import cn from 'classnames'
import PropTypes from 'prop-types'
import {fade, makeStyles} from "@material-ui/core/styles";
import IconButtonTooltip from "../../UI/Tooltips/IconButtonTooltip";
import IconButtonMaterial from "@material-ui/core/IconButton";


const useIconButtonStyles = makeStyles(theme => ({
	root: {
		width: 36,
		padding: 0,
		height: 36,
		borderRadius: theme.shape.borderRadius,
		backgroundColor: theme.palette.additional.light,
		'& svg': {
			margin: 0,
			// opacity: 0.6,
			'& *':{
				// stroke: theme.palette.additional.main,

			}
		},
		'&:hover': {
			backgroundColor: theme.palette.primary.main,
			'& svg': {
				opacity: 1
			},
			'& *': {
				stroke: theme.palette.additional.white
			}
		}
	},
	sizeSmall: {
		width: 26,
		height: 26
	},
	primary: {
		backgroundColor: theme.palette.primary.main,
		'& svg':{
			opacity: 1,
			'& *':{
				stroke: theme.palette.additional.white
			}
		},
		'&:hover': {
			backgroundColor: '#6247FA',
			boxShadow: '0px 2px 6px rgba(21, 28, 86, 0.35);'
		}

	},
	secondary: {
		backgroundColor: 'rgba(66, 62, 250, 0.1)',
		'& svg':{
			opacity: 1,
			'& *':{
				stroke: theme.palette.primary.main
			}
		},
		'&:hover': {
			backgroundColor: theme.palette.primary.main,
			'& svg':{
				'& *':{
					stroke: theme.palette.additional.white
				}
			},
		}
	},
	grey: {
		backgroundColor: theme.palette.additional.light,
		'& svg': {
			'& path': {
				stroke: fade(theme.palette.secondary.main,0.8)
			}
		}
	},
	accentWithoutBg: {
		backgroundColor: 'transparent',
		'& svg': {
			opacity: 1,
			'& *':{
				stroke: theme.palette.primary.main
			}
		}
	},
	lightWithoutBg: {
		backgroundColor: 'transparent',
		'& svg': {
			opacity: 1,
			'& *':{
				stroke: theme.palette.secondary.opacity4
			}
		}
	},
	lightAccentIcon: {
		backgroundColor: fade(theme.palette.primary.main,0.07),
		'& svg': {
			opacity: 1,
			'& *':{
				stroke: theme.palette.primary.main
			}
		},
		'&:hover': {
			backgroundColor: theme.palette.primary.main,
			'& path': {
				stroke: theme.palette.getContrastText(theme.palette.primary.main)
			}
		}
	}
}), {name: "IconButton"});

export default function IconButton({children, onClick, hint = '',type,className, ...rest}) {
	const classes = useIconButtonStyles();
	const customClass = classes[type]
	return (
		<IconButtonTooltip
			title={hint}
		>
				<IconButtonMaterial
					classes={{
						root: classes.root,
						sizeSmall: classes.sizeSmall
					}}
					className={cn('icon-button',customClass,className)}
					onClick={onClick}
					{...rest}
				>
					{children}
				</IconButtonMaterial>
		</IconButtonTooltip>
	);
}
IconButton.proTypes = {
	onClick: PropTypes.func,
}
