import sa from "superagent";

/**
 * @param {number} nodeId
 * @param {entity} nodeOptions
 * @returns {Promise<[]>} a promise resolved to the updated node options
 */
export function updateNodeOptions(nodeId, nodeOptions) {
	return sa.put('/rest/v1/nodes/' + nodeId + '/nodeOptions')
		.set('Content-Type', 'application/json')
		.send(nodeOptions)
		.then(r => r.body);
}

/**
 * @param nodeId {number}
 * @param newSequenceNumber {number} the new sequence number, where all other questions will be incremented if matching or greater. This can be -1 to put the question at the end
 * @returns {Promise<[]>} a promise resolved to the list of node options that were just created
 */
export function createSurveyQuestionForNodeOptionsSurvey(nodeId, newSequenceNumber) {
	return sa.post('/rest/v1/nodes/' + nodeId + '/nodeOptionsSurvey/addQuestion')
		.set('Content-Type', 'application/json')
		.query({
			sequenceNumber: newSequenceNumber,
		})
		.then(r => r.body);
}

/**
 * @param nodeId {number}
 * @param questionId {number}
 * @returns {Promise<[]>} a promise resolved to a boolean indicating success
 */
export function deleteSurveyQuestionForNodeOptionsSurvey(nodeId, questionId) {
	return sa.delete('/rest/v1/nodes/' + nodeId + '/nodeOptionsSurvey/deleteQuestion')
		.set('Content-Type', 'application/json')
		.query({
			questionId: questionId
		})
		.then(r => r.body);
}
