import React from 'react';
import PropTypes from 'prop-types';
import {Redirect, Switch} from "react-router-dom";
import Route from "../../utilities/routing/ProtectedRoute"
import PageNotFoundMessagePage from "./PageNotFoundMessagePage";
import Chat from "../Pages/Chat/Chat";
import SurveyPage from "../../components/Pages/Surveys/Survey";
import TemplatesPage from "../Pages/Templates/Templates";
import {useNavigationDataForProgram} from "../Shared/Providers/NavigationDataProvider";
import ManageWebLinks from "../Pages/LinkTracking/ManageWebLinks";
import ManageWebLinksPage from "../Pages/LinkTracking/ManageWebLinks";

export default function ProgramRouting(props) {
	let {programId} = props;
	useNavigationDataForProgram(programId);
	return (
		<Switch>
			<Route path="/program/:programId(\d+)/choose">
				{/*We're just selecting the program and redirecting to the home page*/}
				<Redirect to="/"/>
			</Route>
			<Route path={"/program/:programId(\\d+)/messageCenter"}>
				<Chat programId={programId}/>
			</Route>
			<Route path={"/program/:programId(\\d+)/surveys"}>
				<SurveyPage programId={programId}/>
			</Route>
			<Route path={"/program/:programId(\\d+)/templates"}>
				<TemplatesPage programId={programId}/>
			</Route>
			<Route path={"/program/:programId(\\d+)/weblinks"}>
				<ManageWebLinksPage programId={programId}/>
			</Route>
			<Route>
				<PageNotFoundMessagePage/>
			</Route>
		</Switch>
	);
}
ProgramRouting.propTypes = {
	programId: PropTypes.number.isRequired,
};
