import React from 'react'
import PropTypes from 'prop-types';
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import BaseSwitch from "../UI/Switch/BaseSwitch";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import makeStyles from "@material-ui/core/styles/makeStyles";
import cn from 'classnames'

const useReportLinkTrackingHeaderStyle = makeStyles(theme => ({
    headerBox: {
        display: 'flex',
        alignItems: 'center',
        '& h1':{
            flex: 1,
            paddingLeft: theme.spacing(1)
        }
    }
}), {name: 'ReportingHeader'});

export default function ReportingHeader(props) {
    const {allowInternal, showInternal, setShowInternal} = props;

    const classes = useReportLinkTrackingHeaderStyle();

    return (
        <Box className={cn(classes.headerBox)}>
            <Typography variant="h1">Choose a Report</Typography>
            {allowInternal && (
                <FormControlLabel
                    label="Show Internal Reports"
                    labelPlacement="start"
                    control={(
                        <BaseSwitch
                            checked={showInternal}
                            onChange={!setShowInternal ? undefined : e => setShowInternal(e.target.checked)}
                        />
                    )}
                />
            )}
        </Box>
    )
}
ReportingHeader.propTypes = {
    allowInternal: PropTypes.bool,
    showInternal: PropTypes.bool,
    setShowInternal: PropTypes.func,
}
