import React from 'react';
import PropTypes from 'prop-types';
import sa from "superagent";
import {useLoginContext} from "./LoginContext";
import {Redirect} from "react-router-dom";
import ProgressButton from "../../components/UI/Buttons/ProgressButton";
import Dialog from "@material-ui/core/Dialog";
import Button from "@material-ui/core/Button";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogContent from "@material-ui/core/DialogContent";

function LogoutControl(props) {
	const {
		logoutControl: LogoutControl = DefaultLogoutButton,
	} = props;

	const loginContext = useLoginContext();

	const [loggedOut, setLoggedOut] = React.useState(false);
	const [working, setWorking] = React.useState(false);
	const [lastError, setLastError] = React.useState(null);
	const [errorDialogOpen, setErrorDialogOpen] = React.useState(false);

	const onError = React.useCallback((error) => {
		setLastError(error);
		setErrorDialogOpen(true);
	}, [setLastError, setErrorDialogOpen]);

	const submitLogout = React.useCallback(() => {
		setWorking(true);
		sa.post('/logout')
			.withCredentials()
			.type('form')
			.set('Do-Not-Redirect', '1')
			.send({})
			.then(r => {
				setErrorDialogOpen(false);
				setLastError(null);
				setLoggedOut(true);
			})
			.catch(e => {
				onError("An unexpected error was encountered while attempting logout.");
			})
			.then(() => setWorking(false));
	}, []);

	React.useEffect(() => {
		if(!working && loggedOut) {
			loginContext.setAuthenticated(false);
		}
	}, [loginContext, loggedOut, working]);

	return (
		<>
			{!loginContext.authenticated && (<Redirect to="/login"/>)}
			<LogoutControl submitLogout={submitLogout}/>
			<Dialog
				fullWidth
				open={errorDialogOpen}
				onClose={() => setErrorDialogOpen(false)}
			>
				<DialogContent>
					<DialogContentText>
						{lastError}
					</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button onClick={() => setErrorDialogOpen(false)} color="primary">
						Dismiss
					</Button>
				</DialogActions>
			</Dialog>
		</>
	);
}
LogoutControl.propTypes = {
	/** Supply this to render a custom control instead of the default button.  It will be supplied a submitLogout callback prop. */
	logoutControl: PropTypes.elementType,
}

export default LogoutControl;

export function AutoLogout(props) {
	return (
		<LogoutControl logoutControl={AutoLogoutControl}/>
	);
}

function DefaultLogoutButton({submitLogout}) {
	return (
		<ProgressButton onClick={submitLogout}>
			Logout
		</ProgressButton>
	);
}

function AutoLogoutControl({submitLogout, children}) {
	React.useEffect(() => submitLogout(), []);
	return null;
}
