import sa from "superagent";

export function loadExternalReports(accountId) {
	return sa.get('/rest/v1/reports')
		.type('form')
		.query({accountId})
		.accept('json')
		.then(r => r.body);
}

export function loadInternalReports(accountId) {
	return sa.get('/rest/v1/reports')
		.type('form')
		.query({accountId, '$query': 'internal'})
		.accept('json')
		.then(r => r.body);
}

export function loadReportDetail(reportName, accountId) {
	return sa.get('/rest/v1/reports/' + reportName)
		.type('form')
		.query({accountId})
		.accept('json')
		.then(r => r.body);
}

export function loadReportQuestionDetail(reportName, questionKey, dependencyValue, accountId) {
	return sa.get('/rest/v1/reports/' + reportName + '/questions/' + questionKey)
		.type('form')
		.query({accountId, dependencyValue})
		.accept('json')
		.then(r => r.body);
}

export function loadReportQuestionAutocompleteSuggestions(reportName, questionKey, questionValue, reportParams) {
	return sa.get('/rest/v1/reports/' + reportName + '/questions/' + questionKey + "/suggestions")
		.type('form')
		.query({questionValue, ...reportParams})
		.accept('json')
		.then(r => r.body);
}

/**
 * @param {string} reportName
 * @param {object|null} params
 * @return {Promise<[]>} a promise resolved to the report data results
 */
export function loadReportData(reportName, params) {
	return sa.get('/rest/v1/reports/' + reportName + '/results')
		.query(params)
		.then(r => r.body);
}
