import React from 'react';
import Button from "@material-ui/core/Button";
import PropTypes from 'prop-types';
import {makeStyles} from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";

const useStyles =  makeStyles(theme => ({
	root: {
		position: "relative",
	},
	progressWrapper: {
		position: "absolute",
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		top: 0,
		left: 0,
		width: "100%",
		height: "100%",
	}
}), {name: "ProgressButton"});

export default function ProgressButton({ type, children, onClick, startIcon, working }) {
	const classes = useStyles();
	return (
		<Button
			variant="contained"
			color="primary"
			size="small"
			startIcon={startIcon}
			onClick={working ? undefined : onClick}
			className={classes.root}
			type={type}
		>
			{!working ? children : (
				<>
					<span>&nbsp;</span>
					<div className={classes.progressWrapper}>
						<CircularProgress disableShrink color="inherit" size={14}/>
					</div>
				</>
			)}
		</Button>
	);
};
ProgressButton.propTypes = {
	/** The button type, such as "submit", default "button" */
	type: PropTypes.string,
	children: PropTypes.node,
	onClick: PropTypes.func,
	startIcon: PropTypes.node,
	/** If true, a progress indicator is shown on the button and the onClick callback is disabled. */
	working: PropTypes.bool,
};
