import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import {fade, makeStyles} from "@material-ui/core/styles";

const useFormGroupStyles = makeStyles(theme => ({
	formGroup: {
		display: 'flex',
		flexDirection: 'column',
		// marginBottom: 20,
		'& label': {
			display: 'inline-flex',
			alignItems: 'center',
			marginBottom: theme.spacing(1),
			fontWeight: 600,
			'& .additional-info': {
				opacity: 0.3,
				marginLeft: theme.spacing(1)
				},
			'& svg': {
				marginLeft: theme.spacing(1),
				'& path': {
					stroke: fade(theme.palette.secondary.main,0.4)
				}
			}
		},
		'& .MuiFormControlLabel-root': { // Checkboxes, etc.
			marginTop: theme.spacing(1),
			'& .MuiFormControlLabel-label': {
				fontWeight: theme.typography.body1.fontWeight,
			},
		},
		'& .MuiFormControl-root': {
			margin: theme.spacing(0),
			'& input': {
				paddingLeft: theme.spacing(8),
			},
			'& .MuiInputAdornment-root': {
				position: 'absolute',
				'& .MuiButtonBase-root': {
					padding: theme.spacing(1)
				}
			}
		},
		'&.active': {
			'& .wrapper': {
				'& .editable':{
					color: theme.palette.secondary.main
				}
			}
		},
		'&.disabled':{
			'& .wrapper': {
				pointerEvents: 'none',
				'& .editable': {

				}
			}
		},
		'& .wrapper':{
			position: 'relative',
			'& .edit-btn':{
				visibility: 'hidden'
			},
			'&:hover':{
				'& .edit-btn': {
					visibility: 'visible'
				},
				'& .editable':{
					color: theme.palette.secondary.main
				}
			},
			'& .actions':{
				position: 'absolute',
				right: 5,
				top: 5,

				'& .icon-button:first-of-type':{
					marginRight: theme.spacing(1)
				},

			},
			'& .editable':{
				width: '100%',
				backgroundColor: fade(theme.palette.secondary.main,0.05),
				color: fade(theme.palette.secondary.main,0.4),
				paddingRight: 70,
				'&:focus': {
					color: theme.palette.secondary.main
				}
			},
		},

		'& textarea': {
			height: 140,
			resize: 'vertical',
			// fontFamily: 'HK Grotesk',
		},
		'& .characters-length': {
			marginTop: 8,
			'& span': {
				opacity: 0.6
			}
		},
		'& input[type=file]': {
			display: 'none'
		},
		'& input, & textarea': {
			fontSize: theme.typography.fontSize,
			fontFamily: 'HK Grotesk',
			padding: '10px 13px',
			borderRadius: 6,
			backgroundColor: fade(theme.palette.getContrastText(theme.palette.primary.main),0.6),
			border: '1px solid rgba(70, 79, 92, 0.2)',
			outline: 'none',
			color: theme.palette.secondary.main,
			'&:disabled': {
				backgroundColor: [[fade(theme.palette.secondary.main,0.015)],'!important'],
				border: '1px solid rgba(70, 79, 92, 0.06)'
			},
			'&:focus': {
				border: '1px solid rgba(70, 79, 92, 0.35)'
			},
			'&::-webkit-input-placeholder': {
				color: fade(theme.palette.secondary.main,0.4)
			}
		}
	},
	gutterBottom: {
		marginBottom: '0.35em',
	},
}), {name: "FormGroup"});

export default function FormGroup(props) {
	let {className, gutterBottom, children} = props;
	const classes = useFormGroupStyles();
	return (
		<div className={cn(
			classes.formGroup,
			{[classes.gutterBottom]: !!gutterBottom},
			'form-group',
			className
		)}>
			{children}
		</div>
	);
}
FormGroup.propTypes = {
	className: PropTypes.string,
	/** If true, add a bottom margin. */
	gutterBottom: PropTypes.bool,
	children: PropTypes.any,
};
