import React, {useEffect} from 'react'
import ManageWebLinksHeader from "../../LinkTracking/ManageWebLinks/Header";
import ManageWebLinksList from "../../LinkTracking/ManageWebLinks/Table";
import PropTypes from "prop-types";
import {useLoadingContext} from "../../Shared/LoaderUtil";
import {loadWeblinks} from "../../Shared/Loaders/WebLinksLoaders";
import {Context} from './context'
import {createNewWeblink, deleteLink} from "../../Shared/Requests/LinkTrackingRequests";

const ManageWebLinks = (props) => {
  let {programId} = props;
  const [webLinks, setWeblinks] = React.useState([]);
  const [isWebLinkNewTemplate, SetWebLinkTemplate] = React.useState(false)
  const [working, setWorking] = React.useState(false);

  const [webLinksCtx] = useLoadingContext(() => {
    const raw = localStorage.getItem('weblinks')
    return loadWeblinks(programId)
        .then(webLinks => {
          (JSON.parse(raw).length > webLinks.length) ?
              setWeblinks(JSON.parse(raw)) :
              setWeblinks(webLinks)

          return webLinks
        })
  }, [])


  useEffect(()=> {
    localStorage.setItem('weblinks', JSON.stringify(webLinks))
  }, [webLinks])

  const {loading} = webLinksCtx;

  const addNewWebLinkTemplate = (event) => {
    if (event) {
      SetWebLinkTemplate(true)
      setWeblinks([
        ...webLinks,
        {
          linkLabel: '',
          originalLink: ''
        }
      ])
    }
  }

  const addWebLink = values => {
    let templateModel = {
      linkLabel: "",
      originalLink: "",
    }

    setWorking(true);

    return createNewWeblink(programId, {...templateModel, ...values})
        .then(r => {
          webLinks.pop()
          setWeblinks([...webLinks, r])
          SetWebLinkTemplate(false)
        })
        .finally(() => setWorking(false))
  }

  const removeWebLink = (programId, id, webLinks) => {
    setWorking(true);
    deleteLink(programId, id)
        .then(() => {
              setWeblinks(webLinks.filter(link => link.id !== id))
            }
        )
        .finally(() => setWorking(false))
  }

  return (
      <Context.Provider value={{addWebLink, isWebLinkNewTemplate, removeWebLink, working}}>
        <>
          <ManageWebLinksHeader
              programId={programId}
              isWebLinkNewTemplate={isWebLinkNewTemplate}
              HandleClick={addNewWebLinkTemplate}
          />
          <ManageWebLinksList
              programId={programId}
              webLinks={webLinks}
              loading={loading}
          />
        </>
      </Context.Provider>
  )
}

ManageWebLinks.propTypes = {
  programId: PropTypes.number.isRequired,
};

export default ManageWebLinks
