import sa from "superagent";

/**
 * @param programId {number}
 * @param linkId {number}
 * @returns {Promise<[]>}
 */
export function deleteLink (programId, linkId) {
    return sa.delete('/rest/v1/programs/' + programId + '/weblinks/' + linkId)
        .set('Content-Type', 'application/json')
        .then(r => r.body);
}

/**
 * @param programId {number}
 * @param newMessageTemplate {object}
 * @returns {Promise<[]>}
 */
export function createNewWeblink(programId, newMessageTemplate) {
    return sa.post('/rest/v1/programs/' + programId + '/weblinks')
        .set('Content-Type', 'application/json')
        .query({
            needShorten: true,
        })
        .send(newMessageTemplate)
        .then(r => r.body);
}

/**
 * @param {number} programId
 * @param {number} linkId
 * @param {object} body
 * @returns {Promise<[]>} a promise resolved to an array of message templates
 */
export function updateLink(programId, linkId, body) {
    return sa.put(`/rest/v1/programs/${programId}/weblinks/${linkId}`)
        .set('Content-Type', 'application/json')
        .send(body)
        .then((r) => {
            return r.body});
}
