import React from "react";
import PropTypes from "prop-types";
import cn from "classnames";
import iphoneX from "../../assets/images/iphone-preview.png";
import makeStyles from "@material-ui/core/styles/makeStyles";
import Box from "@material-ui/core/Box";
import {fade} from "@material-ui/core";

const useIPhoneWrapperStyles = makeStyles(theme => ({
	iphoneBox: {
		marginRight: theme.spacing(4),
		backgroundImage: `url(${iphoneX})`,
		backgroundRepeat: 'no-repeat',
		backgroundPosition: 'center',
		backgroundSize: 'cover',
		width: 275,
		height: 550,
		padding: [[theme.spacing(8), 19, 15, 19]],
	},
	withGutter: {
		"&$withoutBottomContent": {
			paddingBottom: theme.spacing(6),
		},
		"& $chatBox": {
			margin: [[theme.spacing(0), theme.spacing(1), theme.spacing(0), theme.spacing(1)]],
		},
	},
	withoutBottomContent: {
		"& $chatBox": {
			height: '100%',
		}
	},
	withBottomContent: {
		"& $chatBox": {
			height: 'calc(100% - 110px)',
		},
	},
	chatBox: {
		display: 'flex',
		flexDirection: 'column',
		overflow: 'auto',
	},
	bottomBox: {
		backgroundColor: fade(theme.palette.secondary.main,0.05),
		padding: theme.spacing(2),
	},
}), {name: "IPhoneWrapper"});

export default function IPhoneWrapper(props) {
	let {noGutter, bottomContent, children} = props;
	const classes = useIPhoneWrapperStyles();
	return (
		<Box className={cn(classes.iphoneBox, {[classes.withGutter]: !noGutter}, !!bottomContent ? classes.withBottomContent : classes.withoutBottomContent)}>
			<Box className={classes.chatBox}>
				{children}
			</Box>
			{bottomContent && (
				<Box className={classes.bottomBox}>
					{bottomContent}
				</Box>
			)}
		</Box>
	);
}
IPhoneWrapper.propTypes = {
	/** true to exclude padding around the content */
	noGutter: PropTypes.bool,
	bottomContent: PropTypes.any,
	children: PropTypes.any,
};
