import React, { useState } from 'react'
import PropTypes from "prop-types";
import IconButton from "../UI/Buttons/IconButton";
import { ReactComponent as CloseIcon } from "../../assets/svg/Close.svg";
import Grid from "@material-ui/core/Grid";
import TextField from "../UI/Form/TextField";
import TextArea from "../UI/Form/TextArea";
import BaseButton from "../UI/Buttons/BaseButton";
import { ReactComponent as DeleteIcon } from "../../assets/svg/delete.svg";
import { ReactComponent as ProfileIcon } from "../../assets/svg/profile.svg";
import { ReactComponent as EmojiIcon } from "../../assets/svg/emoji.svg";
import { ReactComponent as LinkIcon } from "../../assets/svg/link.svg";
import Typography from "@material-ui/core/Typography";

function CreateTemplateForm({title, handleClose, isEdit = false, onCreate, editValues, onEdit, deleteModal}) {
	let initialValues = {
		label: "",
		subject: "",
		text: ""
	};

	if (editValues) {
		initialValues = {...initialValues, ...editValues}
	}

	const [values, setValues] = useState(initialValues || {});
	const [errors, setErrors] = useState({});

	const handleChange = (event) => {
		const {target} = event;
		const {name, value} = target;
		setValues({...values, [name]: value});
	};

	function submitHandler(event) {
		event.preventDefault()
		setErrors({...errors});
		if (values && !isEdit) {
			onCreate(values)
		}
		if (values && isEdit) {
			onEdit(values)
		}
	}

	function sendFormValidation() {
		return Object.values(values).some(i => i.length <= 0)
	}

	return (
		<div className="create-template-form">
			<div className="header-modal">
				<Typography variant="h1">{title}</Typography>
				<IconButton hint="Close" onClick={handleClose} size="small">
					<CloseIcon/>
				</IconButton>
			</div>
			<form>
				<Grid container xs={12}>
					<Grid item xs={6}>
						<TextField
							defaultValue={isEdit ? 'Mercury bar promotion' : ''}
							label="Name"
							name="label"
							value={values.label}
							onChange={handleChange}
							required
						/>
					</Grid>
				</Grid>
				<Grid container xs={12}>
					<Grid item xs={6}>
						<TextField
							defaultValue={isEdit ? 'Spring holidays!' : ''}
							value={values.subject}
							onChange={handleChange}
							name="subject"
							disabled={isEdit}
							label={<>
								Subject
								<span className="additional-info">Email only</span>
							</>
							}
						/>
					</Grid>
				</Grid>
				<Grid container xs={12}>
					<Grid item xs={12}>
						<TextArea
							defaultValue={isEdit ? 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. ' : ''}
							withCounter={true}
							value={values.text}
							name="text"
							onChange={handleChange}
							label="Text"
						/>
					</Grid>
				</Grid>
				<div className="personalize-block">
					<BaseButton
						type="third-accent"
						startIcon={<ProfileIcon/>}
					>
						Personalize
					</BaseButton>
					<IconButton size="small" hint="Emoji">
						<EmojiIcon/>
					</IconButton>
					<IconButton size="small" hint="Link">
						<LinkIcon/>
					</IconButton>
				</div>
				<div className="footer-form">
					{isEdit && (<BaseButton
						onClick={() => deleteModal(editValues.programId, editValues.id)}
						type="light-error"
						className="delete-button"
						startIcon={<DeleteIcon/>}
					>
						<Typography variant="body2">Delete</Typography>
					</BaseButton>)}
					<BaseButton
						type="link"
						className="cancel-button"
						onClick={handleClose}
					>
						Cancel
					</BaseButton>
					<BaseButton
						type="primary"
						onClick={submitHandler}
						className={'disabled'}
						disabled={sendFormValidation()}
					>
						Save
					</BaseButton>
				</div>
			</form>
		</div>
	);
}

CreateTemplateForm.propTypes = {
	onCreate: PropTypes.func,
	title: PropTypes.string,
	handleClose: PropTypes.func,
	isEdit: PropTypes.bool,
	editValues: PropTypes.object,
	onEdit: PropTypes.func,
	deleteModal: PropTypes.func
}

export default CreateTemplateForm;
