import React, { useState } from 'react'
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {fade} from "@material-ui/core";
import cn from 'classnames'
import SelectField from "../UI/Form/Select";
import { contacts } from "./constants";
import { ReactComponent as ProfileIcon } from "../../assets/svg/profile.svg";

const useContactsListStyle = makeStyles(theme => ({
    wrapper: {
        flexBasis: 240,
        borderRight: `1px solid ${fade(theme.palette.secondary.main, 0.1)}`,
        height: '100%',
        overflow: 'auto'
    },
    headCell: {
        backgroundColor: fade(theme.palette.secondary.main, 0.02),
        height: 56,
        padding: [[theme.spacing(2), theme.spacing(3)]],
        borderBottom: `1px solid ${fade(theme.palette.secondary.main, 0.1)}`
    },
    filterBox: {
        display: 'flex',
        alignItems: 'center',
        // marginTop: theme.spacing(1),
        '& .form-group': {
            marginBottom: theme.spacing(0),
            display: 'inline-flex'
        },
        '& $rootSelect': {
            marginLeft: theme.spacing(2),
            fontWeight: theme.typography.h4.fontWeight,
            fontFamily: theme.typography.h4.fontFamily,
            color: theme.palette.primary.main,
            backgroundColor: 'transparent',
            border: 'none',
            padding: [[theme.spacing(0),theme.spacing(5),theme.spacing(0),theme.spacing(0)]],
            '&:focus': {
                backgroundColor: 'transparent',
            }
        },
        '& path': {
            fill: fade(theme.palette.primary.main,0.4)
        }
    },
    rootSelect: {},
    contactCell: {
        display: 'flex',
        alignItems: 'flex-start',
        backgroundColor: fade(theme.palette.secondary.main, 0.01),
        padding: theme.spacing(2),
        borderBottom: `1px solid ${fade(theme.palette.secondary.main,0.05)}`,
        cursor: 'pointer',
        '&:hover': {
            backgroundColor: fade(theme.palette.primary.main,0.1)
        },
        '&$activeContact': {
            backgroundColor: theme.palette.primary.main,
            '&:hover': {
                backgroundColor: theme.palette.primary.main,
            },
            '& h4': {
                color: theme.palette.getContrastText(theme.palette.primary.main)
            },
            '& h6': {
                color: fade(theme.palette.getContrastText(theme.palette.primary.main),0.8)
            },
            '& $profileInfo span': {
                color: fade(theme.palette.getContrastText(theme.palette.primary.main),0.5)
            },
            '& $profileIcon': {
                backgroundColor: fade(theme.palette.getContrastText(theme.palette.primary.main),0.9),
                '& path': {
                    stroke:  theme.palette.primary.main
                }
            },
            '& $type': {
                backgroundColor: fade(theme.palette.getContrastText(theme.palette.primary.main),0.07),
                color: fade(theme.palette.getContrastText(theme.palette.primary.main),0.6)
            },
            '& $additionalInfo span': {
                color: fade(theme.palette.getContrastText(theme.palette.primary.main),0.6)
            }
        }
    },
    profileInfo: {
        flex: 1,
        '& h6': {
            textTransform: 'uppercase'
        },
        '& span': {
            fontSize: 10
        }
    },
    profileIcon: {
        display: 'inline-flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: 28,
        height: 28,
        borderRadius: '50%',
        backgroundColor: fade(theme.palette.secondary.main,0.1),
        marginRight: theme.spacing(2),
        '& path': {
            stroke: fade(theme.palette.secondary.main,0.3)
        }
    },
    additionalInfo: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        minHeight: 45,
        alignItems: 'flex-end',
        '& span': {
            fontSize: 10
        }
    },
    type: {
        backgroundColor: fade(theme.palette.secondary.main,0.07),
        borderRadius: 2,
        padding: 3,
        fontSize: 9,
        lineHeight: '11px',
        color: fade(theme.palette.secondary.main,0.6),
    },
    activeContact: {

    }
}), {name: 'ContactsList'})


const ContactsList = () => {

    const classes = useContactsListStyle()
    const [activeContact,setActiveContact] = useState(2)

    return (
        <Box className={cn(classes.wrapper)}>
            <Box className={cn(classes.headCell)}>
                <Typography variant="h3">Message Center</Typography>
                <Box className={cn(classes.filterBox)}>
                    <Typography variant="h5" color="textSecondary">Filter by</Typography>
                    <SelectField
                        defaultValue="all"
                        classes={{
                            select: classes.rootSelect
                        }}
                        options={[{
                            label: 'All',
                            value: 'all'
                        }, {
                            label: 'Keyword',
                            value: 'keyword'
                        },{
                            label: 'Campaign name',
                            value: 'campaignName'
                        }]}/>
                </Box>
            </Box>
            <Box>
                {contacts.map((contact,index) => (
                    <Box key={index} className={cn(classes.contactCell,{ [classes.activeContact]: index === activeContact })} onClick={() => setActiveContact(index)}>
                        <Box className={cn(classes.profileIcon)}>
                            <ProfileIcon/>
                        </Box>
                        <Box className={cn(classes.profileInfo)}>
                            <Typography variant="h4">{contact.name ? contact.name : contact.phone}</Typography>
                            <Typography variant="h6" color="textPrimary">{contact.department}</Typography>
                            {contact.name && <Typography variant="caption">{contact.phone}</Typography>}
                        </Box>
                        <Box className={cn(classes.additionalInfo)}>
                            <Box className={cn(classes.type)}>
                                {contact.type}
                            </Box>
                            <Typography variant="caption">{contact.date}</Typography>
                        </Box>
                    </Box>
                ))}
            </Box>
        </Box>
    )
}

export default ContactsList
