import sa from "superagent";

/**
 * @param programId {number}
 * @return {Promise<[]>} a promise resolved to a boolean indicating if at least one chat participant on the program needs a reply
 */
export function loadChatParticipantCountNeedingReply(programId) {
	return sa.get('/rest/v1/programs/' + programId + '/chatParticipants?$query=hasParticipantsNeedingReply')
		.then(r => r.body);
}
