import React from 'react';
import PropTypes from 'prop-types';
import Box from "@material-ui/core/Box";
import makeStyles from "@material-ui/core/styles/makeStyles";
import cn from 'classnames';
import {fade} from "@material-ui/core";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ReportForm from "./Form";
import Typography from "@material-ui/core/Typography";
import Center from "../Shared/Center";

const useReportingContentStyles = makeStyles(theme => ({
    contentBox: {
        display: 'flex',
        marginTop: theme.spacing(2)
    },
    reportListBox: {
        borderRight: `1px solid ${fade(theme.palette.primary.main,0.1)}`,
        paddingRight: theme.spacing(1)
    },
    reportDetailsBox: {
        flex: 1,
        paddingLeft: theme.spacing(2)
    }
}));

export default function ReportingContent(props) {
    const {canManageAccount, reports = [], selectedReportName, setSelectedReportName, selectedReportDetail, reportParams, setReportParam, runReport} = props;
    const classes = useReportingContentStyles();

    // Selected report has most info, but the selectedReportDetail can have additional data (question choices populated)
    const selectedReport = !(reports && selectedReportName) ? undefined : reports.find(r => r.name === selectedReportName);

    // Clear the selected report if it is no longer available (e.g. hiding internal reports when an internal report is selected)
    React.useEffect(() => {
        if(selectedReportName && !selectedReport) {
            setSelectedReportName(null);
        }
    }, [selectedReport, selectedReportName]);

    return (
        <Box className={cn(classes.contentBox)}>
            <Box className={cn(classes.reportListBox)}>
                <List>
                    {reports ? reports.map((report, i) => (
                        <ListItem key={i} selected={report.name === selectedReportName} onClick={() => setSelectedReportName && setSelectedReportName(report.name)}>
                            <ListItemText>{report.label}</ListItemText>
                        </ListItem>
                    )) : (
                        <ListItem disabled>
                            <ListItemText>No Available Reports</ListItemText>
                        </ListItem>
                    )}
                </List>
            </Box>
            <Box className={cn(classes.reportDetailsBox)}>
                {!reports ? (
                    <Center>
                        <Typography>
                            There are no reports available.
                        </Typography>
                    </Center>
                ) : (
                    <ReportForm
                        canManageAccount={canManageAccount}
                        selectedReport={selectedReport}
                        selectedReportDetail={selectedReportDetail}
                        reportParams={reportParams}
                        setReportParam={setReportParam}
                        runReport={runReport}
                    />
                )}
                {/*<pre>{JSON.stringify(selectedReportDetail, null, 2)}</pre>*/}
            </Box>
        </Box>
    )
};
ReportingContent.propTypes = {
    selectedReportName: PropTypes.string,
    setSelectedReportName: PropTypes.func,
    selectedReportDetail: PropTypes.object,
    reports: PropTypes.array,
}
