export default function CustomTheme(props) {
    let {children} = props;
    return (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <CustomCssBaseline/>

            <Helmet>
                <meta name="viewport" content="minimum-scale=1, initial-scale=1, width=device-width"/>
                {/* <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap"/> */}
            </Helmet>
            {children}
        </ThemeProvider>
    );
}
CustomTheme.propTypes = {
    /** The content to render */
    children: PropTypes.any,
};

const useCustomCssBaselineStyles = makeStyles(theme => ({
    "@global": {
        body: {
            "-webkit-font-smoothing": "antialiased",
            "-moz-osx-font-smoothing": "grayscale",
            // '@font-face': [HkGroteskFont],
            color: theme.palette.secondary.main,
            // fontFamily: 'HK Grotesk,'
        },
        code: {
            fontFamily: "source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace",
        },
        ...['article', 'aside', 'details', 'figcaption', 'figure', 'footer', 'header', 'hgroup', 'menu', 'nav', 'section'].reduce((a, c) => {
            // HTML5 display-role reset for older browsers
            a[c] = {display: "block"};
            return a;
        }, {})
    },
}), {name: "CustomCssBaseline"});

function CustomCssBaseline(props) {
    useCustomCssBaselineStyles();
    return null;
}
