import React from 'react'
import PropTypes from "prop-types";
import {makeStyles} from "@material-ui/core/styles";
import {PageTitle} from "../Shared/PageInfo";
import Header from "../Header/Header";
import PageBodyWrapper from "./PageBodyWrapper";
import SideBar from "../SideBar/SideBar";
import PageBodyContainer from "./PageBodyContainer";

const PageContainerContext = React.createContext({
	/** An object containing {label:string, href:string, icon:*|null} for the side bar back button, or null for none */
	sideBarBackButton: null,
	/** The side bar menu options to override the defaults, or null for no override */
	sideBarMenuOptions: null,
	/** A function called with params (newSideBarBackButton:{label:string, href:string, icon:*|null}) to change the side bar back button, where a null param means no back button */
	changeSideBarBackButton: newSideBarBackButton => null,
	/** A function called with params (newSideBarMenuOptions) to change the side bar menu options override, where a null param means no override */
	changeSideBarMenuOptions: newSideBarMenuOptions => null,
});

const usePageContainerStyles = makeStyles(theme => ({
	pageContainer: {
		display: 'flex',
		flexDirection: 'column',
		height: '100vh',
		overflow: 'hidden'
	},
}), {name: "PageContainer"});

export default function PageContainer(props) {
	let {pageTitle, disableNavControls, suppressProgramsMenu, suppressSideBarMenu, activeMenuItem, children} = props;
	const classes = usePageContainerStyles();

	const [sideBarBackButton, setSideBarBackButton] = React.useState(null);
	const [sideBarMenuOptions, setSideBarMenuOptions] = React.useState(null);

	const ctxVal = React.useMemo(() => ({
		sideBarBackButton,
		sideBarMenuOptions,
		changeSideBarBackButton: setSideBarBackButton,
		changeSideBarMenuOptions: setSideBarMenuOptions,
	}), [
		sideBarBackButton,
		sideBarMenuOptions,
	]);

	return (
		<PageContainerContext.Provider value={ctxVal}>
			<div className={classes.pageContainer}>
				{pageTitle && (<PageTitle title={pageTitle}/>)}

				<Header disableAllControls={disableNavControls} isShowProgramsMenu={!suppressProgramsMenu} activeMenuItem={activeMenuItem}/>
				<PageBodyWrapper>
					{!disableNavControls && !suppressSideBarMenu && (
						<SideBar backButton={sideBarBackButton} menuOptions={sideBarMenuOptions}/>
					)}
					<PageBodyContainer>
						{children}
					</PageBodyContainer>
				</PageBodyWrapper>
			</div>
		</PageContainerContext.Provider>
	);
}
PageContainer.propTypes = {
	pageTitle: PropTypes.string,
	/** true to disable navigation controls, such as on the login screen */
	disableNavControls: PropTypes.bool,
	/** true to suppress the programs menu */
	suppressProgramsMenu: PropTypes.bool,
	/** true to suppress the side bar menu */
	suppressSideBarMenu: PropTypes.bool,
	/** If supplied, a header menu item will be treated as "active" if its label matches this string. */
	activeMenuItem: PropTypes.string,
	children: PropTypes.any,
};

export function CustomSideBarMenuOptions({backButton, menuOptions}) {
	const {sideBarBackButton, sideBarMenuOptions, changeSideBarBackButton, changeSideBarMenuOptions} = React.useContext(PageContainerContext);

	React.useEffect(() => {
		changeSideBarBackButton(backButton);
		// Restore original button
		return () => changeSideBarBackButton(sideBarBackButton);
	}, [JSON.stringify(backButton)]);

	React.useEffect(() => {
		changeSideBarMenuOptions(menuOptions);
		// Restore original options
		return () => changeSideBarMenuOptions(sideBarMenuOptions);
	}, [JSON.stringify(menuOptions)]);

	return null;
}
CustomSideBarMenuOptions.propTypes = {
	backButton: PropTypes.shape({
		label: PropTypes.string.isRequired,
		href: PropTypes.string.isRequired,
		icon: PropTypes.node,
	}),
	menuOptions: PropTypes.array,
};
