import PropTypes from "prop-types";
import React from "react";
import CircularProgress from "@material-ui/core/CircularProgress";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {fade} from "@material-ui/core/styles";

const useLoadingOverlayStyles = makeStyles(theme => ({
	container: {
		position: "absolute",
		top: 0,
		left: 0,
		width: "100%",
		height: "100%",
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		backgroundColor: fade(theme.palette.text.primary, 0.1),
	},
	progressWrapper: {
		backgroundColor: fade(theme.palette.text.primary, 0.5),
		color: fade(theme.palette.getContrastText(theme.palette.text.primary), 0.8),
		padding: theme.spacing(3),
		borderRadius: theme.shape.borderRadius,
	},
}), {name: "LoadingOverlay"});

export default function LoadingOverlay(props) {
	let {loading, children} = props;
	const classes = useLoadingOverlayStyles();
	return (
		<div style={{position: "relative"}}>
			{children}
			{loading && (
				<div className={classes.container}>
					<div className={classes.progressWrapper}>
						<CircularProgress color="inherit"/>
					</div>
				</div>
			)}
		</div>
	);
}
LoadingOverlay.propTypes = {
	loading: PropTypes.bool,
	children: PropTypes.any,
};
