import sa from "superagent";

/**
 * @return {Promise<>} a promise resolved to the current user
 */
export function loadCurrentUser() {
	return sa.get('/rest/v1/user')
		.then(r => r.body);
}

/**
 * @param programId {number}
 * @param activeOnly {boolean}
 * @return {Promise<>} a promise resolved to the users with access to the program
 */
export function loadProgramUsers(programId, activeOnly) {
	return sa.get('/rest/v1/programs/' + programId + '/users')
		.query({
			"$activeOnly": activeOnly,
		})
		.then(r => r.body);
}

/**
 * @param accountId {number}
 * @param userId {number}
 * @return {Promise<>} a promise resolved to the user account permission, if any
 */
export function loadUserAccountPermission(accountId, userId) {
	return sa.get('/rest/v1/accounts/' + accountId + '/users/' + userId + '/userAccountPermissions')
		.then(r => r.body);
}

/**
 * @param programId {number}
 * @param userId {number}
 * @return {Promise<>} a promise resolved to the user account permission, if any
 */
export function loadUserAccountPermissionByProgram(programId, userId) {
	return sa.get('/rest/v1/programs/' + programId + '/users/' + userId + '/userAccountPermissions')
		.then(r => r.body);
}

/**
 * @param programId {number}
 * @param userId {number}
 * @return {Promise<>} a promise resolved to the user program permission, if any
 */
export function loadUserProgramPermission(programId, userId) {
	return sa.get('/rest/v1/programs/' + programId + '/users/' + userId + '/userProgramPermissions')
		.then(r => r.body);
}
