import React from 'react'
import Box from '@material-ui/core/Box';
import AreaChart from '../../UI/Charts/AreaChart';
import makeStyles from '@material-ui/core/styles/makeStyles';
import cn from 'classnames'
import {useLoadingContext} from '../../Shared/LoaderUtil';
import {loadLinkReport} from '../../Shared/Loaders/WebLinksLoaders';

const useClickReportChart = makeStyles(theme => ({
    chartBox: {
        marginTop: theme.spacing(2),
        marginLeft: -theme.spacing(3)
    }
}));

const prepareChartData = reports => {
    const data = [];

    reports.forEach(report => {
        data.push({
            name: report?.label,
            data: report?.linkClicks.map(day => {
                return [new Date(day?.date).getTime(), day?.clicks]
            })
        })
    });

    return data;
};

const ClickReport = (props) => {
    const {webLinkId, programId, version} = props;

    const [reportData, setReportData] = React.useState([]);

    useLoadingContext(
        () => loadLinkReport(programId, webLinkId)
            .then(data => {
                setReportData(prepareChartData(data));
            }), [version]
    );

    const classes = useClickReportChart();
    return (
        <Box className={cn(classes.chartBox)}>
            <AreaChart series={reportData} version={version}/>
        </Box>
    )
};

export default ClickReport
