import React from "react";
import PropTypes from "prop-types";
import {Helmet} from "react-helmet";

export function PageTitle(props) {
	let {title} = props;
	let fullTitle = "IRIO" + (title ? " - " + title : "");
	return (
		<Helmet>
			<title>{fullTitle}</title>
		</Helmet>
	);
}
PageTitle.propTypes = {
	title: PropTypes.string,
};
