import React from "react";

/**
 * @return {{loading:boolean, result:*|null, reason:*|null, updateVersion:number}}
 */
export function createEmptyLoadingContext(loading = false) {
	return {
		loading: loading,
		reason: null,
		result: null,
		updateVersion: 0,
	};
}

/**
 * A hook to use a loading function (promiseFn) that will be invoked whenever 'deps' changes.  The behavior is similar
 * to useEffect() with respect to the function and deps arguments.  The current loading context and a setter are
 * returned, similar to useState().
 *
 * Note that the deps must be specified correctly (as is true for useEffect()) or the hook will not function properly.
 * I.e. absent values from deps will cause updates to promiseFn to be ignored.
 *
 * @return {[{loading:boolean, result:*|null, reason:*|null, updateVersion:number},{setLoadingContext:function}]}
 */
export function useLoadingContext(promiseFn, deps, options = null) {
	let {
		/** {boolean} true if the promise function is enabled, or false if it shouldn't be called right now. This also dictates the initial value for "loading" */
		enabled = true,
	} = options || {};

	const [loadingContext, setLoadingContext] = React.useState(() => createEmptyLoadingContext(enabled));
	const requestCountRef = React.useRef(0);

	function setVal(key, val) {
		setLoadingContext(prev => ({...prev, [key]: val, updateVersion: prev.updateVersion + 1}));
	}

	React.useEffect(() => {
		if (enabled) {
			// Get the current count, so we will only use a response from the most recent request
			let currReqCount = ++requestCountRef.current;

			setVal("loading", true);
			promiseFn()
				.then(response => {
					if (currReqCount === requestCountRef.current) {
						setVal("result", response);
						setVal("loading", false);
					}
				})
				.catch(reason => {
					if (currReqCount === requestCountRef.current) {
						setVal("reason", reason);
						setVal("loading", false);
					}
				});
		}
		/*
		 * Ignoring missing dep 'promiseFn' because the contract of this function tells the caller that this function
		 * can ignore new versions of 'promiseFn' if the 'deps' argument did not change.  This is similar to the rules
		 * of using useEffect() itself.
		 */
		// eslint-disable-next-line
	}, [...deps, enabled]);

	React.useEffect(() => {
		// Since we're unmounting, disregard any further responses
		return () => { requestCountRef.current = -1; };
	}, []);

	return [loadingContext, setLoadingContext];
}

/**
 * Create a new loading context from multiple. Only regenerate the results when a "loadingContext.updateVersion" changes
 * @param loadingContextArray {object[]} the array of loading context values. This should NOT change length on
 *        subsequent invocations because it is used in "deps" for React.useMemo()
 * @param resultFactory {function} called with no params and returns the result
 * @return {{loading:boolean, result:*|null, reason:*|null, updateVersion:number}}
 */
export function useNewLoadingContextFromMany(loadingContextArray, resultFactory) {
	const deps = loadingContextArray.map(ctx => ctx.updateVersion);
	return React.useMemo(() => {
		return newLoadingContextFromMany(loadingContextArray, resultFactory());
	}, deps);
}

/**
 * Create a new loading context from multiple. This only joins the values of "reason" and "loading".
 * The "result" must be provided.
 * @param loadingContextArray {object[]}
 * @param result {object[]} a new loading context
 * @return {{loading:boolean, result:*|null, reason:*|null, updateVersion:number}}
 */
export function newLoadingContextFromMany(loadingContextArray, result) {
	return {
		loading: !!loadingContextArray.find(ctx => ctx.loading),
		reason: !!loadingContextArray.find(ctx => ctx.reason) ? "At least one request failed" : null,
		result,
		updateVersion: loadingContextArray.map(ctx => ctx.updateVersion).reduce((a,c) => a + c),
	};
}
