import React from "react";
import PropTypes from "prop-types";
import moment from 'moment-timezone';

const timeZone = moment.tz.guess();
const PATTERN_DATE_TIME      = "YYYY/MM/DD hh:mm A";
const PATTERN_DATE_TIME_SEC  = "YYYY/MM/DD hh:mm:ss A";
const PATTERN_DATE           = "YYYY/MM/DD";
const PATTERN_DATE_TIME_TEXT = "MMMM D, YYYY [at] h:mmA";
const PATTERN_DATE_TEXT      = "MMMM D, YYYY";

export function LocalizedDateTime(props) {
	let {isoDate, includeTime = true, noZone, noSeconds, numerical = true} = props;
	if (!isoDate) {
		return null;
	}

	let momentObj = moment(isoDate).tz(timeZone);
	if (includeTime) {
		let fmt = numerical ? (noSeconds ? PATTERN_DATE_TIME : PATTERN_DATE_TIME_SEC) : PATTERN_DATE_TIME_TEXT;
		return momentObj.format(fmt + (noZone ? "" : " z"));
	} else {
		let fmt = numerical ? PATTERN_DATE : PATTERN_DATE_TEXT;
		return momentObj.format(fmt + (noZone ? "" : " z"));
	}
}
LocalizedDateTime.propTypes = {
	isoDate: PropTypes.string,
	/** true to include the time, default true */
	includeTime: PropTypes.bool,
	/** true to exclude time zone */
	noZone: PropTypes.bool,
	/** true to exclude seconds */
	noSeconds: PropTypes.bool,
	/** true to use numerical format, default true */
	numerical: PropTypes.bool,
};
