import React from "react";
import PropTypes from "prop-types";

import {ReactComponent as ArrowLeftIcon} from "../../assets/svg/arrowTriangleLeft.svg";
import {ReactComponent as ArrowRightIcon} from "../../assets/svg/arrowTriangleRight.svg";

import cn from 'classnames';
import moment from "moment-timezone";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import TableFooter from "@material-ui/core/TableFooter";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import makeStyles from "@material-ui/core/styles/makeStyles";
import Alert from "@material-ui/lab/Alert";
import {fade} from "@material-ui/core";

const timeZone = moment.tz.guess();

const useSurveyStyle = makeStyles(theme => ({
	dense: {
		'& td, & th': {
			padding: [[theme.spacing(1),theme.spacing(1),theme.spacing(1),theme.spacing(0)]],
		}
	},
	headCell: {
		padding: [[theme.spacing(2),theme.spacing(1),theme.spacing(2),0]]
	},
	footerRow: {
		'& th': {
			fontFamily: theme.typography.h2.fontFamily,
			fontSize: theme.typography.body1.fontSize,
			backgroundColor: fade(theme.palette.secondary.main,0.05),
			fontWeight: 'bold',
			'&:first-of-type': {
				paddingLeft: theme.spacing(1),
			}
		}
	},
}), {name: 'ReportDataResults'})

export default function ReportDataResults(props) {
	let {reportData, onChangePageNum, dense} = props;
	let {headers, results, footers} = reportData;
	let {content} = results;

	const classes = useSurveyStyle();

	function formatFriendly(value, index) {
		let {typeHint} = headers[index];
		if (typeHint === "isoDate" && !!value) {
			let dateVal = moment(value).tz(timeZone);
			//return dateVal.format("LLL z");
			return dateVal.format("YYYY/MM/DD hh:mm A z");
		} else if (typeof value === "boolean") {
			return "" + value;
		} else {
			return value;
		}
	}

	let {totalElements} = results;
	let isPage = !!totalElements || totalElements === 0;

	return (
		<>
			{content.length === 0 ? (
				<Alert severity="info">No results</Alert>
			) : (
				<>
					<Table className={cn({[classes.dense]: dense})}>
						<TableHead>
							<TableRow>
								{headers.map((header, headerIdx) => (
									<TableCell key={headerIdx} className={classes.headCell}>{header.label}</TableCell>
								))}
							</TableRow>
						</TableHead>
						<TableBody>
							{content.map((row, rowIdx) => (
								<TableRow key={rowIdx}>
									{row.map((cell, cellIdx) => (
										<TableCell key={cellIdx}>{formatFriendly(cell, cellIdx)}</TableCell>
									))}
								</TableRow>
							))}
						</TableBody>

						{footers && (
							<TableFooter>
								<TableRow className={classes.footerRow}>
									{footers.map((footer, footerIdx) => (
										<TableCell key={footerIdx} component="th">{formatFriendly(footer, footerIdx)}</TableCell>
									))}
								</TableRow>
							</TableFooter>
						)}
					</Table>

					{isPage && (
						<Box mt={2}>
							<PageControls page={results} onChangePageNum={onChangePageNum}/>
						</Box>
					)}
				</>
			)}
		</>
	);
}
ReportDataResults.propTypes = {
	reportData: PropTypes.shape({
		headers: PropTypes.arrayOf(PropTypes.shape({
			label: PropTypes.string.isRequired,
			typeHint: PropTypes.string,
		})).isRequired,
		/** The page or slice of results */
		results: PropTypes.shape({
			/** The page/slice results */
			content: PropTypes.array.isRequired,
			/** The page/slice number */
			number: PropTypes.number.isRequired,
			/** The page/slice size */
			size: PropTypes.number.isRequired,
			/** The total number of pages */
			totalPages: PropTypes.number,
			/** The total number of elements on all pages */
			totalElements: PropTypes.number,
		}).isRequired,
		footers: PropTypes.array,
	}).isRequired,
	/** Called with params (newPageNum:number) to change the page number. "newPageNum" is zero-based. */
	onChangePageNum: PropTypes.func,
	/** true for cells with less padding */
	dense: PropTypes.bool,
};

function PageControls(props) {
	let {page, onChangePageNum} = props;
	let {number, size, totalPages, totalElements} = page;

	let currentPage = number + 1;
	let firstRecordOnPage = totalElements === 0 ? 0 : number * size + 1;
	let lastRecordOnPage = (number + 1) * size;
	if (totalElements < lastRecordOnPage) {
		lastRecordOnPage = totalElements;
	}

	let prevBtnActive = currentPage > 1;
	let nextBtnActive = currentPage < totalPages;

	return (
		<>
			{onChangePageNum && (
				<>
					<Box component="span" mr={2}>
						<Button
							variant="contained"
							color="primary"
							size="small"
							disabled={!prevBtnActive}
							onClick={e => onChangePageNum(number - 1)}
							startIcon={<ArrowLeftIcon/>}
						>
							Previous
						</Button>
					</Box>
					<Box component="span" mr={2}>
						<Button
							variant="contained"
							color="primary"
							size="small"
							disabled={!nextBtnActive}
							onClick={e => onChangePageNum(number + 1)}
							endIcon={<ArrowRightIcon/>}
						>
							Next
						</Button>
					</Box>
				</>
			)}

			<Box component="span" mt={2}>
				Page <b>{currentPage}</b> of <b>{totalPages}</b>
				{' '}
				(<b>{firstRecordOnPage}</b>-<b>{lastRecordOnPage}</b> of <b>{totalElements}</b>)
			</Box>
		</>
	);
}
PageControls.propTypes = {
	/** The page or slice of results */
	page: PropTypes.shape({
		/** The page/slice number */
		number: PropTypes.number.isRequired,
		/** The page/slice size */
		size: PropTypes.number.isRequired,
		/** The total number of pages */
		totalPages: PropTypes.number.isRequired,
		/** The total number of elements on all pages */
		totalElements: PropTypes.number.isRequired,
	}).isRequired,
	/** Called with params (newPageNum:number) to change the page number. "newPageNum" is zero-based. */
	onChangePageNum: PropTypes.func,
};
