import React, {useState} from 'react'
import cn from 'classnames'
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import InfoDropDown from "../../UI/DropDown/InfoDropDown";
import makeStyles from "@material-ui/core/styles/makeStyles";
import BaseButton from "../../UI/Buttons/BaseButton";
import {ReactComponent as PieChartIcon} from '../../../assets/svg/pirChart.svg'
import {ReactComponent as PlusIcon} from '../../../assets/svg/Plus.svg'
import ModalContent from "../../UI/Modal/ModalContent";
import HeaderModal from "../../UI/Modal/HeaderModal";
import {ReactComponent as RefreshIcon} from "../../../assets/svg/refresh.svg";
import ClickReport from "../../LinkTracking/ManageWebLinks/ClickReport";
import MaterialModal from "../../UI/Modal/Modal";
import FooterModal from "../../UI/Modal/FooterModal";
import PropTypes from "prop-types";

const useWebLinksHeaderStyle = makeStyles(theme => ({
    headerBox: {
        display: 'flex',
        alignItems: 'flex-end',
        '& h1': {
            flex: 1
        },
        '& button': {
            marginLeft: theme.spacing(1)
        }
    },
    footerModal: {
        '& a': {
            minWidth: 180,
            marginLeft: theme.spacing(1)
        }
    }
}), {name: 'WebLinksHeader'})


const ManageWebLinksHeader = ({programId, HandleClick, isWebLinkNewTemplate}) => {
    const classes = useWebLinksHeaderStyle()
    const [reportDataVersion, setReportDataVersion] = React.useState([0]);
    const [openModal, setOpenModal] = useState()

    return (
        <Box className={cn(classes.headerBox)}>
            <Typography variant="h1">Manage Web Links</Typography>
            <InfoDropDown/>
            <BaseButton
                onClick={() => {
                    setOpenModal(true);
                    setReportDataVersion(0);
                }}
                type="light-accent"
                startIcon={<PieChartIcon/>}
            >
                Show Full Report
            </BaseButton>
            <BaseButton
                type="primary"
                startIcon={<PlusIcon/>}
                onClick={HandleClick}
                disabled = {isWebLinkNewTemplate}
            >
                Add Weblink
            </BaseButton>
            <MaterialModal
                open={openModal}
                onClose={() => setOpenModal(false)}
            >
                <ModalContent>
                    <HeaderModal title="Full Click Report" handleClose={() => setOpenModal(false)}>
                        <BaseButton
                            startIcon={<RefreshIcon/>}
                            type="third-main"
                            onClick={() => {
                                setReportDataVersion(reportDataVersion + 1);
                            }}
                        >
                            Refresh
                        </BaseButton>
                    </HeaderModal>
                    <ClickReport programId={programId} version={reportDataVersion}/>
                    <FooterModal className={cn(classes.footerModal)}>
                        <BaseButton
                            type="light-accent"
                            href={'/reporting'}
                        >
                            Advanced Reporting
                        </BaseButton>
                        <BaseButton
                            type="primary"
                            href={'/'}
                        >
                            Add to Dashboard
                        </BaseButton>
                    </FooterModal>
                </ModalContent>
            </MaterialModal>
        </Box>
    )
}

ManageWebLinksHeader.propTypes = {
    programId: PropTypes.number.isRequired,
};

export default ManageWebLinksHeader
