import sa from "superagent";

/**
 * @param {number} programId
 * @param {string} handlerName
 * @return {Promise<[]>} a promise resolved to an array of nodes
 */
export function loadNodesByProgramAndHandlerName(programId, handlerName) {
	return sa.get('/rest/v1/programs/' + programId + '/nodes')
		.query({
			handler: handlerName,
			"$activeOnly": true,
		})
		.then(r => r.body);
}

/**
 * @param {number} nodeId
 * @returns {Promise<[]>} a promise resolved to an array of nodeOptions
 */
export function loadNodeOptions(nodeId) {
	return sa.get('/rest/v1/nodes/' + nodeId + '/nodeOptions')
		.then(r => r.body);
}

/**
 * @param {number} nodeId
 * @param {string} mediumId
 * @return {Promise<[]>}
 */
export function loadNodeResponsePreview(nodeId, mediumId) {
	return sa.get('/rest/v1/nodes/' + nodeId + '/previews/' + mediumId)
		.then(r => r.body);
}
