import React from 'react';
import PropTypes from 'prop-types';
import {Redirect, Switch} from "react-router-dom";
import Route from "../../utilities/routing/ProtectedRoute"
import PageNotFoundMessagePage from "./PageNotFoundMessagePage";
import {useNavigationDataForProgramGroup} from "../Shared/Providers/NavigationDataProvider";

export default function ProgramGroupRouting(props) {
	let {programGroupId} = props;
	useNavigationDataForProgramGroup(programGroupId);
	return (
		<Switch>
			<Route path="/programGroup/:programGroupId(\d+)/choose">
				{/*We're just selecting the program group and redirecting to the home page*/}
				<Redirect to="/"/>
			</Route>
			<Route>
				<PageNotFoundMessagePage/>
			</Route>
		</Switch>
	);
}
ProgramGroupRouting.propTypes = {
	programGroupId: PropTypes.number.isRequired,
};
