import React from "react";
import PropTypes from "prop-types";

export default function Twemoji(props) {
	let {text} = props;
	const elemRef = React.useRef();

// TODO We don't need to use Twemoji in Safari
/* TODO this will only work if static/js/emoji/twemoji.js is already included. Maybe we should use a React library, such as react-twemoji instead?
	React.useEffect(() => {
		twemoji.parse(elemRef.current, {base: 'https://unpkg.com/emoji-datasource-apple@4.0.4/img/apple/', folder: '64', ext: '.png'})
	});
*/
	return (<span ref={elemRef}>{text}</span>);
}
Twemoji.propTypes = {
	/** The text that may contain some emojis */
	text: PropTypes.string.isRequired,
};
