import React, {useCallback, useState} from 'react'
import TemplatesHeader from "./TemplatesHeader";
import {makeStyles} from "@material-ui/core/styles";
import cn from "classnames";
import TemplatesList from "./TemplatesList";
import MaterialModal from '../UI/Modal/Modal'
import ModalContent from "../UI/Modal/ModalContent";
import CreateTemplateForm from "./CreateTemplateForm";
import {useLoadingContext} from "../Shared/LoaderUtil";
import {loadTemplates} from "../Shared/Loaders/TemplateLoaders";
import Alert from "@material-ui/lab/Alert";
import CenterProgress from "../Shared/CenterProgress";
import PropTypes from "prop-types";
import {createNewMessageTemplate, deleteTemplate, ediMessageTemplate} from "../Shared/Requests/TemplateRequests";
import LoadingOverlay from "../Shared/LoadingOverlay";

const useTemplatesContentStyles = makeStyles(theme => ({
	templatesWrapper: {},
	templateModalForm: {
		'& .create-template-form': {
			'& .form-group': {
				marginBottom: theme.spacing(4)
			},
			'& .header-modal': {
				display: 'flex',
				justifyContent: 'space-between',
				marginBottom: theme.spacing(3),
				'& .icon-button': {
					borderRadius: '50%'
				},
			},
			'& .textarea-group': {
				marginBottom: 0
			},
			'& .personalize-block': {
				display: 'flex',
				justifyContent: 'flex-end',
				marginBottom: theme.spacing(3),
				'& button': {
					marginLeft: theme.spacing(2),
				}
			},
			'& .footer-form': {
				display: 'flex',
				backgroundColor: theme.palette.additional.light,
				margin: [[theme.spacing(0),-theme.spacing(6),-theme.spacing(6),-theme.spacing(6)]],
				padding: [[theme.spacing(4),theme.spacing(6),theme.spacing(6),theme.spacing(6)]],
				borderRadius: [[0,0,15,15]],
				'& .cancel-button':{
					marginLeft: 'auto'
				},
				'& button': {
					minWidth: 120,
				},
				// '& .delete-button': {
				// 	marginRight: 'auto',
				// }
			}
		}
	}
}), {name: "TemplatesContent"});

export default function TemplatesContent(props) {
	let {programId} = props;
	const [templates, setTemplates] = React.useState([]);
	const [editTemplate, setEditTemplate] = React.useState({});

	const [templatesCtx] = useLoadingContext(
		() => loadTemplates(programId)
			.then(templates => {
				setTemplates(templates)
				return templates
			}), []
	);

	const ready = templatesCtx.loading;
	const classes = useTemplatesContentStyles();
	const [openTemplateModal, setOpenTemplateModal] = useState(false)
	const [openEditModal, setOpenEditModal] = useState(false)

	const handleOpenTemplateModal = useCallback(() => {
		setOpenTemplateModal(true)
	}, [])

	const handleCloseTemplateModal = useCallback(() => {
		setOpenTemplateModal(false)
	}, [])

	function addTemplate(values) {
		let templateModel = {
			subject: '',
			label: '',
			text: '',
		}
		templateModel.programId = programId
		let resultValues = {...templateModel, ...values}

		return createNewMessageTemplate(programId, resultValues)
			.then(r => {
				templates.push(r)
				setTemplates(templates)
				handleCloseTemplateModal()
			});
	}

	function editCreatedTemplate(values) {
		let templateModel = {
			label: '',
			text: '',
		}

		templateModel.programId = programId
		let resultValues = {}
		resultValues.text = values.text
		resultValues.label = values.label

		return ediMessageTemplate(programId, values.id, resultValues)
			.then(r => {
				const updatedTemplates = templates.map(template =>
					template.id === r.id ? {...template, ...r} : template);
				setTemplates(updatedTemplates)
				setOpenEditModal(false)
			});
	}

	function removeTemplate(programId, id) {
		deleteTemplate(programId, id).then(() =>
			setTemplates(templates.filter(template => template.id !== id))
		)
	}

	function setEditValue(e, template) {
		setOpenEditModal(true)
		setEditTemplate(template)
	}

	return (
		<div className={cn(classes.templatesWrapper)}>
			<TemplatesHeader
				openModal={handleOpenTemplateModal}
			/>
			{ready ? (
				<CenterProgress/>
			) : (!templates.length && !ready) ? (
				<Alert severity="info">No results</Alert>
			) : (
				<TemplatesList
					openEditModal={setEditValue}
					openModal={handleOpenTemplateModal}
					deleteModal={removeTemplate}
					templates={templates}
				/>
			)}

			<MaterialModal
				open={openTemplateModal}
				onClose={handleCloseTemplateModal}
			>
				<ModalContent classes={cn(classes.templateModalForm)}>
					<CreateTemplateForm
						title="New Template"
						onCreate={addTemplate}
						handleClose={handleCloseTemplateModal}
					/>
				</ModalContent>
			</MaterialModal>
			<MaterialModal
				open={openEditModal}
				onClose={() => setOpenEditModal(false)}
			>
				<ModalContent classes={cn(classes.templateModalForm)}>
					<CreateTemplateForm
						editValues={editTemplate}
						isEdit={true}
						onEdit={editCreatedTemplate}
						deleteModal={removeTemplate}
						title="Mercury bar promotion"
						handleClose={() => setOpenEditModal(false)}
					/>
				</ModalContent>
			</MaterialModal>
		</div>
	);
}

TemplatesContent.propTypes = {
	programId: PropTypes.number.isRequired,
};
