import React from 'react'
import PropTypes from 'prop-types'
import {ReactComponent as DeleteIcon} from "../../assets/svg/delete.svg";
import IconButton from "../UI/Buttons/IconButton";
import Typography from "@material-ui/core/Typography";
import Box from '@material-ui/core/Box'

export default function TemplateRow({template, openDeleteModal, openEditModal }) {
    return (
        <div className="template-row" onClick={openEditModal}>
            <Box className="info">
                <Typography variant="h5" className="template-title">{template.label}</Typography>
                <Typography variant="body1" className="template-description">{template.text}</Typography>
            </Box>
            <div className="template-actions">
                <IconButton
                    size="small"
                    hint="Delete"
                    onClick={openDeleteModal}
                >
                    <DeleteIcon/>
                </IconButton>
            </div>
        </div>
    );
}
TemplateRow.propTypes = {
    template: PropTypes.object
}
