import React from "react";

/**
 * A hook that functions similarly to React.useState(), but it returns three arguments.
 *
 * The argument is the same as to React.useState()
 *
 * The return consists of:
 *   - the current value of state (same as useState() return position 0)
 *   - a "setState" function that merges in state updates similar to class based setState() calls
 *   - a "replaceState" function which replaces the state (same as useState() return position 1)
 */
export function useClassicState(initial) {
	const [state, replaceState] = React.useState(initial);

	const setState = React.useCallback(
		(stateUpdateArg) => {
			if(typeof stateUpdateArg === 'function') {
				replaceState(os => ({...os, ...stateUpdateArg(os)}));
			} else {
				replaceState(os => ({...os, ...stateUpdateArg}));
			}
		},
		[] // replaceState is guaranteed by React to be stable
	);

	return [state, setState, replaceState];
}

/**
 * A hook that uses a simple state update to force a rerender.
 *
 * Returns a forceUpdate function that can be called to trigger a rerender.
 */
export function useForceUpdate() {
	const [value, setValue] = React.useState(0);
	return () => setValue(value => value + 1);
}
