import sa from "superagent";

/**
 * @param accountId {number}
 * @return {Promise<[]>} a promise resolved to the account
 */
export function loadAccount(accountId) {
	return sa.get('/rest/v1/accounts/' + accountId)
		.then(r => r.body);
}

/**
 * @param activeOnly {boolean} false to include inactive accounts
 * @return {Promise<[]>} a promise resolved to a list of the accounts available to the user
 */
export function loadAccounts(activeOnly = true) {
	return sa.get('/rest/v1/accounts')
		.query({
			"$activeOnly": activeOnly,
		})
		.then(r => r.body);
}
