import sa from "superagent";

/**
 * @param programId {number}
 * @param templateId {number}
 * @returns {Promise<[]>}
 */
export function deleteTemplate(programId, templateId) {
    return sa.delete('/rest/v1/programs/' + programId + '/messageTemplates/' + templateId)
        .set('Content-Type', 'application/json')
        .then(r => r.body);
}

/**
 * @param programId {number}
 * @param newMessageTemplate {object}
 * @returns {Promise<[]>}
 */
export function createNewMessageTemplate(programId, newMessageTemplate) {
    return sa.post('/rest/v1/programs/' + programId + '/messageTemplates')
        .set('Content-Type', 'application/json')
        .send(newMessageTemplate)
        .then(r => r.body);
}

/**
 * @param programId {number}
 * @param templateId {number}
 * @param newMessageTemplate {object}
 * @returns {Promise<[]>}
 */
export function ediMessageTemplate(programId, templateId, newMessageTemplate) {
    return sa.patch('/rest/v1/programs/' + programId + '/messageTemplates/' + templateId)
        .set('Content-Type', 'application/json')
        .send(newMessageTemplate)
        .then(r => r.body);
}
