import React from 'react'
import FormGroup from "../../UI/Form/FormGroup";
import {ReactComponent as ArrowDownIcon} from "../../../assets/svg/arrowDown.svg";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select/Select";
import { ReactComponent as HelpIcon } from "../../../assets/svg/help.svg";
import IconButtonTooltip from "../../UI/Tooltips/IconButtonTooltip";

const SelectField = ({label,options = [],info,children,customSelect,...rest}) => {


    return (
        <FormGroup>
            {label && <label>{label} {info && <IconButtonTooltip title={info}>
                <HelpIcon/>
            </IconButtonTooltip>}</label>}
            <Select
                IconComponent={ArrowDownIcon}
                {...rest}
            >
                {customSelect ? children : options.map((option, i) => <MenuItem key={i} value={option.value}>{option.label}</MenuItem>)}
            </Select>
        </FormGroup>
    )
}

export default SelectField
