import React from 'react'
import Box from "@material-ui/core/Box";
import ContactsList from "./ContactsList";
import makeStyles from "@material-ui/core/styles/makeStyles";
import cn from 'classnames'
import {fade} from "@material-ui/core";
import Conversations from "./Conversation";

const useMessageCenter = makeStyles(theme => ({
    wrapper: {
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.getContrastText(theme.palette.secondary.main),0.8),
        display: 'flex',
        height: '100%',
        overflow: 'hidden'
    }
}),{ name : 'MessageCenter' })

const MessageCenter = () => {

    const classes = useMessageCenter()

    return (
        <Box className={cn(classes.wrapper)}>
            <ContactsList/>
            <Conversations type="messageCenter"/>
        </Box>
    )
}

export default MessageCenter
