import React from 'react'
import DropDown from "../../UI/DropDown/DropDown";
import {ReactComponent as HelpIcon} from "../../../assets/svg/help.svg";
import IconButton from "../../UI/Buttons/IconButton";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {dropDownOptions} from "../../Header/constants";

const useInfoDropDownStyle = makeStyles(theme => ({
    infoDropDown: {
        backgroundColor: 'rgba(21, 28, 86, 0.05)',
        '& svg': {
            opacity: 0.6
        },
        '&:hover': {
            backgroundColor: theme.palette.primary.main,
            '& svg': {
                opacity: 1
            },
        }
    }
}), {name: 'InfoDropdown'})

export default function InfoDropDown({options = dropDownOptions}) {

    const classes = useInfoDropDownStyle()

    return (
        <DropDown options={options}>
            <IconButton hint="Info" className={classes.infoDropDown}>
                <HelpIcon/>
            </IconButton>
        </DropDown>
    )
}
