import React, {useCallback, useState, useContext} from 'react'
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableBody from "@material-ui/core/TableBody";
import TableRow from "@material-ui/core/TableRow";
import ManageLinkTableRow from "../../LinkTracking/ManageWebLinks/TableRow";
import makeStyles from "@material-ui/core/styles/makeStyles";
import cn from 'classnames'
import MaterialModal from "../../UI/Modal/Modal";
import ModalContent from "../../UI/Modal/ModalContent";
import HeaderModal from "../../UI/Modal/HeaderModal";
import {ReactComponent as RefreshIcon} from "../../../assets/svg/refresh.svg";
import BaseButton from "../../UI/Buttons/BaseButton";
import ClickReport, {prepareChartData} from "../../LinkTracking/ManageWebLinks/ClickReport";
import DeleteModal from "../../UI/Modal/DeleteModal";
import CenterProgress from "../../Shared/CenterProgress";
import Alert from "@material-ui/lab/Alert";
import {Context} from './context'
import LoadingOverlay from "../../Shared/LoadingOverlay";


const useManageWebLinksListStyle = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(4),
    marginTop: theme.spacing(2),
    '& table': {
      tableLayout: 'fixed'
    },
  },
  tableCellHead: {
    paddingTop: 0
  },
}))

const ManageWebLinksList = (props) => {
  let {programId, isAdded, handleCreate , webLinks , loading} = props;
  const {addWebLink, removeWebLink, working} = useContext(Context)
  let id;
  const [webLinkId, setWebLinkId] = React.useState([]);
  const [reportDataVersion, setReportDataVersion] = React.useState([0]);
  const [editLink, setEditLink] = React.useState(false);
  const classes = useManageWebLinksListStyle()
  const [openModal, setOpenModal] = useState(false)
  const [openDeleteModal, setOpenDeleteModal] = useState(false)

  const handleOpenDeleteModal = useCallback((e, weblink) => {
    id = weblink.id
    e.preventDefault()
    e.stopPropagation()
    setOpenDeleteModal(true)
  }, [])

  const handleCloseDeleteModal = useCallback(() => {
    setOpenDeleteModal(false)
  }, [])

  const handleConfirmDeleteModal = useCallback((webLinks) => {
    removeWebLink(programId, id, webLinks)
    setOpenDeleteModal(false)
  }, [])

  return (
      <LoadingOverlay loading={working}>
      <Paper elevation={0} className={cn(classes.paper)}>
        {loading ? (
            <CenterProgress/>
        ) : (!webLinks) ? (
            <Alert severity="info">No results</Alert>
        ) : (
            webLinks.length ?
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell className={cn(classes.tableCellHead)}>Title</TableCell>
                      <TableCell className={cn(classes.tableCellHead)}>Link</TableCell>
                      <TableCell className={cn(classes.tableCellHead)}>Bitly Short Link</TableCell>
                      <TableCell className={cn(classes.tableCellHead)}></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {webLinks.map((webLink) => (
                        <ManageLinkTableRow
                            key={webLink.id}
                            webLink={webLink}
                            openDeleteModal={(event) => handleOpenDeleteModal(event, webLink)}
                            onCreate={addWebLink}
                            isAdded={editLink}
                            openClickReport={() => {
                              setWebLinkId(webLink?.id);
                              setReportDataVersion(0);
                              setOpenModal(true);
                            }}
                        />
                    ))}
                  </TableBody>
                </Table> :
                <Alert severity="info">No results</Alert>
        )}

        <MaterialModal
            open={openModal}
            onClose={() => setOpenModal(false)}
        >
          <ModalContent>
            <HeaderModal title="Click Report" handleClose={() => setOpenModal(false)}>
              <BaseButton
                  startIcon={<RefreshIcon/>}
                  type="third-main"
                  onClick={() => {
                    setReportDataVersion(reportDataVersion + 1);
                  }}
              >
                Refresh
              </BaseButton>
            </HeaderModal>
            <ClickReport webLinkId={webLinkId} programId={programId} version={reportDataVersion}/>
          </ModalContent>
        </MaterialModal>
        <MaterialModal
            open={openDeleteModal}
            onClose={handleCloseDeleteModal}
        >
          <DeleteModal
              text="Are you sure you want to delete the webLink?"
              handleCancel={handleCloseDeleteModal}
              handleConfirm={() => handleConfirmDeleteModal(webLinks)}
          />
        </MaterialModal>
      </Paper>
      </LoadingOverlay>
  )
}

export default ManageWebLinksList
