import React, {useState, useCallback} from 'react'
import cn from 'classnames'
import {fade, makeStyles} from "@material-ui/core/styles";
import TemplateRow from "./TemplateRow";
import MaterialModal from "../UI/Modal/Modal";
import DeleteModal from "../UI/Modal/DeleteModal";
import Paper from '@material-ui/core/Paper'

const useTemplatesListStyles = makeStyles(theme => ({
	templatesList: {
		'& .template-row': {
			borderRadius: theme.shape.borderRadius,
			backgroundColor: theme.palette.additional.light,
			padding: [[theme.spacing(3), theme.spacing(2), theme.spacing(3), theme.spacing(2)]],
			display: 'flex',
			alignItems: 'center',
			flexWrap: 'wrap',
			cursor: 'pointer',
			'&:hover': {
				backgroundColor: fade(theme.palette.secondary.main,0.1),
				// '& .template-title': {
				//
				// },
			},
			'& .info': {
				flex: 1,
			},
			'& .template-actions': {
				marginLeft: theme.spacing(1)
			},
			'&:not(:last-of-type)':{
				marginBottom: theme.spacing(2),
			},
			'& .template-title': {
				fontFamily: 'HK Grotesk Medium',
				fontWeight: 500,
				marginRight: theme.spacing(4),
			},
			'& .template-description': {
				flex: 1,
				width: '100%',
				opacity: 0.4
			},
			'& .icon-button': {
				backgroundColor: 'rgba(100, 62, 250, 0.07)',
				'&:hover':{
					backgroundColor: theme.palette.primary.main,
					'& path':{
						stroke: theme.palette.additional.white
					}
				},
				'& svg':{
					opacity: 1,
				},
				'& path':{
					stroke: theme.palette.primary.main
				}
			}
		}
	},
}), {name: "TemplatesList"});

export default function TemplatesList({templates = [], openEditModal, deleteModal}) {
	let programId
	let id
	const classes = useTemplatesListStyles();
	const [openDeleteModal, setOpenDeleteModal] = useState(false)

	const handleOpenDeleteModal = useCallback((e, template) => {
		programId = template.programId
		id = template.id

		e.preventDefault()
		e.stopPropagation()
		setOpenDeleteModal(true)
	}, [])

	const handleCloseDeleteModal = useCallback(() => {
		setOpenDeleteModal(false)
	}, [])

	const handleConfirmDeleteModal = useCallback(() => {
		deleteModal(programId, id)
		setOpenDeleteModal(false)
	}, [])

	return (
		<Paper
			elevation={0}
			className={cn(classes.templatesList)}
		>
			{templates.map((template, index) => (
				<TemplateRow
					key={index}
					openEditModal={(event) => openEditModal(event, template)}
					openDeleteModal={(event) => handleOpenDeleteModal(event, template)}
					template={template}
				/>
			))}
			<MaterialModal
				open={openDeleteModal}
				onClose={handleCloseDeleteModal}
			>
				<DeleteModal
					text="Are you sure you want to delete the template?"
					handleCancel={handleCloseDeleteModal}
					handleConfirm={() => handleConfirmDeleteModal()}
				/>
			</MaterialModal>
		</Paper>
	);
}
