import React from 'react'
import {Redirect, useLocation} from "react-router-dom";
import sa from "superagent";
import TextField from "../UI/Form/TextField";
import ProgressButton from "../UI/Buttons/ProgressButton";
import {makeStyles} from "@material-ui/core/styles";
import {useLoginContext} from "../../utilities/login/LoginContext";
import Fade from "@material-ui/core/Fade";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles(theme => ({
	root: {
		maxWidth: "380px",
		margin: "2rem auto",
	},
	error: {
		marginTop: theme.spacing(4),
	}
}), {name: "LoginForm"});

export default function LoginForm(props) {
	const classes = useStyles();

	let location = useLocation();

	const loginContext = useLoginContext();

	const [username, setUsername] = React.useState("");
	const [password, setPassword] = React.useState("");
	const [loggedIn, setLoggedIn] = React.useState(false);

	const [working, setWorking] = React.useState(false);

	const [lastError, setLastError] = React.useState(null);
	const [displayError, setDisplayError] = React.useState(false);

	const onError = React.useCallback((error) => {
		setLastError(error);
		setDisplayError(true);
	}, [setLastError, setDisplayError]);

	let { from } = location.state || { from: { pathname: "/" } };

	const submitLogin = React.useCallback(() => {
		if(username === '' || password === '') {
			onError("You must fill in both the Email and Password fields.");
		} else {
			setWorking(true);
			sa.post('/login')
				.type('form')
				.accept('json')
				.set('Do-Not-Redirect', '1')
				.send({username, password})
				.then(r => {
					if (r.body && r.body.error) {
						onError(r.body.error);
					} else {
						setDisplayError(false);
						setLastError(null)
						setLoggedIn(true);
					}
				})
				.catch(e => {
					console.error("An unexpected error was encountered while attempting login.", e.message, e.response);
					onError("An unexpected error was encountered while attempting login.");
				})
				.then(() => setWorking(false));
		}
	}, [username, password]);

	React.useEffect(() => {
		if(!working && loggedIn) {
			loginContext.setAuthenticated(true);
		}
	}, [loginContext, loggedIn, working]);

	return loginContext.authenticated ? (
		<Redirect to={from}/>
	) : (
		<form onSubmit={e => { e.preventDefault(); submitLogin(); }}>
			<div className={classes.root}>
				<TextField label="Email" value={username} onChange={e => setUsername(e.target.value)} type="email" gutterBottom/>
				<TextField label="Password" value={password} onChange={e => setPassword(e.target.value)} type="password" gutterBottom/>
				<ProgressButton type="submit">Login</ProgressButton>
				<Fade in={displayError}>
					<Typography className={classes.error} color="error">{lastError}</Typography>
				</Fade>
			</div>
		</form>
	);
}
