import React from 'react'
import PropTypes from "prop-types";
import FormGroup from "../../UI/Form/FormGroup";
import IconButtonTooltip from "../Tooltips/IconButtonTooltip";
import {ReactComponent as HelpIcon} from "../../../assets/svg/help.svg";
import CheckBox from "../CheckBox/CheckBox";

export default function TextField({name, label, info, labelStyle, gutterBottom, optional, onChange, value, customLeadingContent, ...rest}) {
	const [fieldEnabled, setFieldEnabled] = React.useState(!optional);

	React.useEffect(() => {
		if(onChange && !fieldEnabled && value !== "") {
			onChange({target: {value: ""}}); // onChange is expecting an event
		}
	}, [fieldEnabled, onChange, value]);

	return (
		<FormGroup gutterBottom={gutterBottom}>
			{(label || optional || info) && (
				<label style={labelStyle}>
					{optional && (
						<CheckBox
							color="primary"
							checked={fieldEnabled}
							onChange={e => setFieldEnabled(e.target.checked)}
						/>
					)}{label || "Unnamed Field"} {info && (
						<IconButtonTooltip title={info}>
							<HelpIcon/>
						</IconButtonTooltip>
					)}
				</label>
			)}
			{customLeadingContent && customLeadingContent({fieldEnabled})}
			{fieldEnabled ? (
				<input
					name={name}
					value={value || ""}
					onChange={onChange}
					{...rest}
				/>
			) : (
				<input
					disabled
					value="None"
					onChange={() => {}}
				/>
			)}

		</FormGroup>
	);
}
TextField.propTypes = {
	name: PropTypes.string,
	label: PropTypes.node,
	labelStyle: PropTypes.object,
	/** If true, add a bottom margin. */
	gutterBottom: PropTypes.bool,
	/** If true, a checkbox will be rendered to conditionally disable the field.  Disabled fields will provide an empty string */
	optional: PropTypes.bool,
	/** render function for custom content to add above the input, will receive a fieldEnabled prop */
	customLeadingContent: PropTypes.func,
}
