import sa from "superagent";

/**
 * @param programGroupId {number}
 * @return {Promise<[]>} a promise resolved to the program group
 */
export function loadProgramGroup(programGroupId) {
	return sa.get('/rest/v1/programGroups/' + programGroupId)
		.then(r => r.body);
}

/**
 * @param accountId {number}
 * @param activeOnly {boolean} false to include inactive program groups
 * @returns {Promise<[]>} a promise resolved to the distinct program groups
 */
export function loadProgramGroupsByAccount(accountId, activeOnly) {
	return sa.get('/rest/v1/accounts/' + accountId + '/programGroups')
		.query({
			"$activeOnly": activeOnly,
		})
		.then(r => r.body);
}

/**
 * @param accountId {number}
 * @param keys {string[]}
 * @returns {Promise<[]>} a promise resolved to program group data values on the account
 */
export function loadProgramGroupDataByAccountAndKeys(accountId, keys) {
	return sa.get('/rest/v1/accounts/' + accountId + '/programGroupDatas')
		.query({
			"$query": "allDataByKeys",
			key: keys,
		})
		.then(r => r.body);
}

/**
 * @param accountId {number}
 * @param key {string}
 * @returns {Promise<[]>} a promise resolved to the distinct program group data values
 */
export function loadUniqueProgramGroupDataValuesByAccountAndKey(accountId, key) {
	return sa.get('/rest/v1/accounts/' + accountId + '/programGroupDatas')
		.query({
			"$query": "valuesByKey",
			key: key,
		})
		.then(r => r.body);
}

/**
 * @param userId {number}
 * @param accountId {number}
 * @return {Promise<[]>} a promise resolved to the user's favorite program groups on the account
 */
export function loadFavoriteProgramGroupsByAccount(userId, accountId) {
	return sa.get('/rest/v1/users/' + userId + '/favoriteProgramGroups/' + accountId)
		.then(r => r.body);
}
