import React from 'react'
import Switch from "@material-ui/core/Switch";
import {makeStyles} from "@material-ui/core/styles";
import { ReactComponent as IconUnchecked } from '../../../assets/svg/minus.svg'
import { ReactComponent as IconChecked } from '../../../assets/svg/check.svg'
import cn from 'classnames'

const useBaseSwitchStyles = makeStyles(theme => ({
    root: {
        width: 42,
        height: 21,
        padding: 0,
        overflow: 'visible'
    },
    switchBase: {
        left: 1,
        top: 1,
        padding: 1,
        '& .icon': {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            width: 17,
            height: 17,
            borderRadius: '50%',
            '&.unchecked':{
                backgroundColor: theme.palette.primary.main,
            },
            '&.checked-icon': {
                backgroundColor: theme.palette.additional.white,
                '& svg': {
                    marginTop: 0,
                }
            },
        },


        '&$checked': {
            transform: 'translateX(21px)',
            color: theme.palette.secondary.main,
            '& + $track': {
                backgroundColor: theme.palette.primary.main,
                opacity: 1,
            },
            '& $thumb': {
                backgroundColor: theme.palette.additional.white,
            },
        },
    },
    thumb: {
        width: 19,
        height: 19,
        backgroundColor: theme.palette.primary.main,
    },
    track: {
        borderRadius: 26 / 2,
        border: `1px solid ${theme.palette.primary.main}`,
        backgroundColor: theme.palette.additional.white,
        opacity: 1,
    },
    checked: {
    },
    // focusVisible: {},
}), {name: "BaseSwitch"});

export default function BaseSwitch(props) {

	const classes = useBaseSwitchStyles();

	return (
		<Switch
            checkedIcon={
                <div className="checked-icon icon">
                    <IconChecked/>
                </div>}
            icon={<div className="unchecked icon">
                <IconUnchecked/>
            </div>}
			classes={classes}
			className={cn(props.className)}
            {...props}
		/>
	);
}
