import React, { useState } from 'react'
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import cn from 'classnames'
import makeStyles from "@material-ui/core/styles/makeStyles";
import Divider from "@material-ui/core/Divider";
import Box from "@material-ui/core/Box";
import BaseButton from "../UI/Buttons/BaseButton";
import {ReactComponent as DownloadIcon} from "../../assets/svg/download.svg";
import {ReactComponent as TableIcon} from "../../assets/svg/table.svg";
import {ReactComponent as ChartIcon} from "../../assets/svg/pirChart.svg";
import {ReactComponent as LineChartIcon} from "../../assets/svg/lineChart.svg";
import {ReactComponent as BarChartIcon} from "../../assets/svg/barChart.svg";
import LinkReportTrackingModal from "./LinkReportTrackingModal";
import SampleBarChart from "./SampleBarChart";
import OutgoingMessagesModal from "./OutgoingMessagesModal";
import SendMeMonthlyReportModal from "./SendMeMonthlyReport";
import Center from "../Shared/Center";
import CenterProgress from "../Shared/CenterProgress";
import {Question} from "./FormQuestions";
import TablePreviewModal from "./TablePreviewModal";

const useReportFormStyles = makeStyles(theme => ({
    paper: {
        padding: theme.spacing(4)
    },
    formBox: {
        '& .form-group': {
            maxWidth: 250,
            marginTop: theme.spacing(4),
            marginBottom: theme.spacing(2)
        }
    },
    actionsBox: {
        marginBottom: theme.spacing(2),
        '& button': {
            marginRight: theme.spacing(2)
        }
    },
    modalBodyBox: {
        marginLeft: -theme.spacing(3),
        width: 'calc(100% + 20px)',
        '& .apexcharts-legend-series': {
            display: [['flex'],'!important'],
            alignItems: 'center'
        },
        '& .position-right': {
            justifyContent: [['center'], '!important']
        },
        '& +div': {
            '& button': {
                minWidth: 180
            }
        }
    },
}), {name: 'ReportForm'});

export default function ReportForm(props) {
    const {canManageAccount, selectedReport, selectedReportDetail, reportParams, setReportParam, runReport} = props;

    const classes = useReportFormStyles();

    const [results, setResults] = useState(null);

    const [openTableModal, setOpenTableModal] = useState(false);
    const [openChartModal, setOpenChartModal] = useState(false);
    const [openLineChart, setOpenLineChartModal] = useState(false);
    const [openBarChart, setOpenBarChartModal] = useState(false);
    const [openMonthlyReport, setOpenMonthlyModal] = useState(false);

    function showTablePreview() {
        runReport(selectedReportDetail.name, reportParams)
            .then(results => setResults(results))
            .then(() => setOpenTableModal(true))
            .catch(r => console.error("could not run report", r));
    }

    return !selectedReport ? (
        <Center>
            <Typography>
                Please select a report.
            </Typography>
        </Center>
    ) : (!selectedReportDetail || !reportParams) ? (
        <CenterProgress/>
    ) : (
        <Paper elevation={0} className={cn(classes.paper)}>
            <Typography variant="h3">{selectedReport.label || 'Report'}</Typography>
            <Divider variant="middle"/>
            {selectedReport.description && (
                <Typography variant="body1">
                    {selectedReport.description}
                </Typography>
            )}
            <Box className={cn(classes.formBox)}>
                {selectedReportDetail.questions.map((question, i) => ((
                    <Question key={i} report={selectedReportDetail} question={question} reportParams={reportParams} setReportParam={setReportParam} canManageAccount={canManageAccount}/>
                )))}
                <Divider variant="middle"/>
                <Box className={cn(classes.actionsBox)}>
                    <BaseButton type="primary" onClick={() => setOpenMonthlyModal(true)}>Schedule Monthly Report</BaseButton>
                    <BaseButton
                        type="third-accent-text"
                        startIcon={<DownloadIcon/>}
                    >
                        Download Excel Table
                    </BaseButton>
                    <BaseButton
                        type="third-accent-text"
                        startIcon={<TableIcon/>}
                        onClick={showTablePreview}
                    >
                        Table Preview
                    </BaseButton>
                    <BaseButton
                        type="third-accent-text"
                        startIcon={<ChartIcon/>}
                        onClick={() => setOpenChartModal(true)}
                    >
                        Chart Preview
                    </BaseButton>
                    <BaseButton
                        type="third-accent-text"
                        startIcon={<LineChartIcon/>}
                        onClick={() => setOpenLineChartModal(true)}
                    >
                        Line Chart
                    </BaseButton>
                    <BaseButton
                        type="third-accent-text"
                        startIcon={<BarChartIcon/>}
                        onClick={() => setOpenBarChartModal(true)}
                    >
                        Bar Chart
                    </BaseButton>
                </Box>
                <Typography variant="caption">
                    Monthly Reports will be run for "last month's" activity, as applicable.
                </Typography>
                <LinkReportTrackingModal open={openBarChart} handleClose={() => setOpenBarChartModal(false)} contentClass={classes.modalBodyBox}/>
                <SampleBarChart open={openLineChart} handleClose={() => setOpenLineChartModal(false)} contentClass={classes.modalBodyBox}/>
                <OutgoingMessagesModal open={openChartModal} handleClose={() => setOpenChartModal(false)} contentClass={classes.modalBodyBox}/>
                <TablePreviewModal open={openTableModal} handleClose={() => setOpenTableModal(false)} reportData={results} title={selectedReportDetail.label} />
                <SendMeMonthlyReportModal open={openMonthlyReport} handleClose={() => setOpenMonthlyModal(false)}/>
            </Box>

        </Paper>
    )
}

