import React from 'react'
import PropTypes from "prop-types";
import PageContainer from "../PageContainer";
import TemplatesContent from "../../Templates/Templates";

export default function TemplatesPage(props) {
	let {programId} = props;
	return (
		<PageContainer
			pageTitle="Templates"
			activeMenuItem="Messages"
		>
			<TemplatesContent programId={programId}/>
		</PageContainer>
	);
}
TemplatesPage.propTypes = {
	programId: PropTypes.number.isRequired,
};
