import React from "react";
import PropTypes from "prop-types";
import {useLoadingContext} from "../LoaderUtil";
import {loadAccount, loadAccounts} from "../Loaders/AccountLoaders";
import {loadProgramGroup, loadProgramGroupsByAccount} from "../Loaders/ProgramGroupLoaders";
import {loadProgram, loadProgramsByProgramGroup} from "../Loaders/ProgramLoaders";
import {useLoginContext} from "../../../utilities/login/LoginContext";
import {useCurrentUser} from "./CurrentUserProvider";
import ls from "local-storage";

const NavigationDataContext = React.createContext({
	/** Called with no params to reload the navigation data */
	reloadNavigationData: () => null,
	/** Called with params (accountId:number) to change the account id */
	changeAccountId: accountId => null,
	/** Called with params (programGroupId:number) to change the program group id */
	changeProgramGroupId: programGroupId => null,
	/** Called with params (programId:number) to change the program id */
	changeProgramId: programId => null,
	/** true if data is loading */
	loading: false,
	/** All accounts available to the current user */
	allAccounts: [],
	/** All program groups available to the current user on the selected account */
	allProgramGroups: [],
	/** Active program groups available to the current user on the selected account. This is provided in addition to "all" program groups because "active" is determined by having an "active" program, and that information is not available in "allProgramGroups" */
	activeProgramGroups: [],
	/** All programs available to the current user on the selected program group */
	allPrograms: [],
	/** The account, once loaded */
	account: null,
	/** The program group, once loaded */
	programGroup: null,
	/** The program, once loaded */
	program: null,
});

/**
 * @return {{reloadNavigationData:function, changeAccountId:function, changeProgramGroupId:function, changeProgramId:function, loading:boolean, allAccounts:array, allProgramGroups:array, activeProgramGroups:array, allPrograms:array, account:object|null, programGroup:object|null, program:object|null}} the navigation data and available actions
 */
export function useNavigationData() {
	return React.useContext(NavigationDataContext);
}

/**
 * @return {{reloadNavigationData:function, changeAccountId:function, changeProgramGroupId:function, changeProgramId:function, loading:boolean, allAccounts:array, allProgramGroups:array, activeProgramGroups:array, allPrograms:array, account:object|null, programGroup:object|null, program:object|null}} the navigation data and available actions, where data should match the provided account ID
 */
export function useNavigationDataForAccount(accountId) {
	let navData = useNavigationData();
	let {changeAccountId} = navData;
	React.useEffect(() => {
		changeAccountId(accountId);
	}, [accountId]);
	return navData;
}

/**
 * @return {{reloadNavigationData:function, changeAccountId:function, changeProgramGroupId:function, changeProgramId:function, loading:boolean, allAccounts:array, allProgramGroups:array, activeProgramGroups:array, allPrograms:array, account:object|null, programGroup:object|null, program:object|null}} the navigation data and available actions, where data should match the provided program group ID
 */
export function useNavigationDataForProgramGroup(programGroupId) {
	let navData = useNavigationData();
	let {changeProgramGroupId} = navData;
	React.useEffect(() => {
		changeProgramGroupId(programGroupId);
	}, [programGroupId]);
	return navData;
}

/**
 * @return {{reloadNavigationData:function, changeAccountId:function, changeProgramGroupId:function, changeProgramId:function, loading:boolean, allAccounts:array, allProgramGroups:array, activeProgramGroups:array, allPrograms:array, account:object|null, programGroup:object|null, program:object|null}} the navigation data and available actions, where data should match the provided program ID
 */
export function useNavigationDataForProgram(programId) {
	let navData = useNavigationData();
	let {changeProgramId} = navData;
	React.useEffect(() => {
		changeProgramId(programId);
	}, [programId]);
	return navData;
}

/**
 * A <NavigationDataProvider/>, but only if the user is logged in
 */
export function NavigationDataProviderIfAuthenticated(props) {
	let {children} = props;
	const {authenticated} = useLoginContext();
	if (!authenticated) {
		return children;
	}
	return (
		<NavigationDataProvider>
			{children}
		</NavigationDataProvider>
	);
}
NavigationDataProviderIfAuthenticated.propTypes = {
	children: PropTypes.any,
};

/**
 * Provide the selected account, program group, and program via a React.Context.
 * Additionally provide lists of each to choose from.
 * ID setting and individual entity loading trickles up, while list loading trickles down.
 * When an program ID, program group ID, or account ID is set, load the corresponding value and update the parent
 * entity's ID if necessary. Then load all available entities, and auto-set the child entity to the first available if
 * it is not already set to something.
 */
export function NavigationDataProvider(props) {
	let {children} = props;
	const user = useCurrentUser();

	// Even if we're authenticated, "user" might be null while the record is loading right after login
	const userId = user ? user.id : -1;
	const storageKey = React.useMemo(() => "user." + userId + ".nav", [userId]);

	const [navigationState, setNavigationState] = React.useState(() => {
		ls.backend(localStorage);
		return {
			accountId: null,
			programGroupId: null,
			programId: null,
			...ls.get(storageKey),
		};
	});

	const firstRender = React.useRef(true);
	const {accountId, programGroupId, programId} = navigationState;

	React.useEffect(() => {
		if(firstRender.current) {
			firstRender.current = false;
		} else {
			ls.backend(localStorage);
			ls.set(storageKey, navigationState)
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [storageKey, JSON.stringify(navigationState)]);

	function setNavigationValue(key, valueArg) {
		if(typeof valueArg === 'function') {
			setNavigationState(os => ({...os, [key]: valueArg(os[key])}));
		} else {
			setNavigationState(os => ({...os, [key]: valueArg}));
		}
	}

	function setAccountId(newVal) {
		setNavigationValue('accountId', newVal);
	}

	function setProgramGroupId(newVal) {
		setNavigationValue('programGroupId', newVal);
	}

	function setProgramId(newVal) {
		setNavigationValue('programId', newVal);
	}

	function changeProgramId(newProgramId) {
		setProgramId(newProgramId);
	}

	function changeProgramGroupId(newProgramGroupId) {
		// Set the program group ID. If it doesn't match the current program group ID, clear the selected program.
		// A default program will be automatically chosen.
		if (newProgramGroupId !== programGroupId) {
			changeProgramId(null);
			setProgramGroupId(newProgramGroupId);
		}
	}

	function changeAccountId(newAccountId) {
		// Set the account ID. If it doesn't match the current account ID, clear the selected program group and program.
		// A default program group and program will be automatically chosen.
		if (newAccountId !== accountId) {
			changeProgramGroupId(null);
			setAccountId(newAccountId);
		}
	}

	function autoChooseFromList(list, setterFn) {
		if (list.length > 0) {
			// If no value is selected, we'll auto-choose one
			setterFn(prevId => !!prevId ? prevId : list[0].id);
		}
	}

	function onAccountsLoaded(accountList) {
		autoChooseFromList(accountList.filter(a => a.active), setAccountId);
	}
	function onActiveProgramGroupsLoaded(activeProgramGroupList) {
		autoChooseFromList(activeProgramGroupList, setProgramGroupId);
	}
	function onProgramsLoaded(programList) {
		autoChooseFromList(programList.filter(p => p.active), setProgramId);
	}

	const [refreshTime, setRefreshTime] = React.useState(0);

	const [allAccountsCtx] = useAllAccountsLoader(refreshTime, onAccountsLoaded);
	const [allProgramGroupsCtx] = useProgramGroupsLoader(accountId, false, refreshTime, () => null);
	// We're making a separate request for active program groups because the "active" value cannot be determined from
	// the entities themselves. "active" is based upon having at least one active program.
	const [activeProgramGroupsCtx] = useProgramGroupsLoader(accountId, true, refreshTime, onActiveProgramGroupsLoaded);
	const [allProgramsCtx] = useAllProgramsLoader(programGroupId, refreshTime, onProgramsLoaded);
	const [accountCtx] = useAccountLoader(accountId, refreshTime);
	const [programGroupCtx] = useProgramGroupLoader(programGroupId, refreshTime);
	const [programCtx] = useProgramLoader(programId, refreshTime);

	const {result: allAccounts} = allAccountsCtx;
	const {result: allProgramGroups} = allProgramGroupsCtx;
	const {result: activeProgramGroups} = activeProgramGroupsCtx;
	const {result: allPrograms} = allProgramsCtx;
	// The following is checking if the ID is set before using the context result. This is in case the ID was cleared
	// out, so we're not using the previously loaded value.
	const account = !!accountId ? accountCtx.result : null;
	const programGroup = !!programGroupId ? programGroupCtx.result : null;
	const program = !!programId ? programCtx.result : null;

	React.useEffect(() => {
		if (programGroup) {
			setAccountId(programGroup.accountId);
		}
	}, [programGroup]);

	React.useEffect(() => {
		if (program) {
			setProgramGroupId(program.programGroupId);
		}
	}, [program]);

	const loading = allAccountsCtx.loading || allProgramGroupsCtx.loading || allProgramsCtx.loading || accountCtx.loading || programGroupCtx.loading || programCtx.loading;

	const ctxVal = React.useMemo(() => ({
		reloadNavigationData: () => setRefreshTime(Date.now()),
		changeAccountId,
		changeProgramGroupId,
		changeProgramId,
		loading,
		allAccounts: allAccounts || [],
		allProgramGroups: allProgramGroups || [],
		activeProgramGroups: activeProgramGroups || [],
		allPrograms: allPrograms || [],
		account,
		programGroup,
		program,
	}), [
		loading,
		allAccounts,
		allProgramGroups,
		activeProgramGroups,
		allPrograms,
		account,
		programGroup,
		program,
	]);

	return (
		<NavigationDataContext.Provider value={ctxVal}>
			{children}
		</NavigationDataContext.Provider>
	);
}
NavigationDataProvider.propTypes = {
	children: PropTypes.any,
};

/**
 * A <NavigationDataProvider/> with mock data, such as for example components
 */
export function MockNavigationDataProvider(props) {
	let {allAccounts, allProgramGroups, activeProgramGroups, allPrograms, account, programGroup, program, children} = props;

	const ctxVal = React.useMemo(() => ({
		reloadNavigationData: () => null,
		changeAccountId: accountId => null,
		changeProgramGroupId: programGroupId => null,
		changeProgramId: programId => null,
		loading: false,
		allAccounts,
		allProgramGroups,
		activeProgramGroups,
		allPrograms,
		account,
		programGroup,
		program,
	}), [
		allAccounts,
		allProgramGroups,
		activeProgramGroups,
		allPrograms,
		account,
		programGroup,
		program,
	]);

	return (
		<NavigationDataContext.Provider value={ctxVal}>
			{children}
		</NavigationDataContext.Provider>
	);
}
MockNavigationDataProvider.propTypes = {
	allAccounts: PropTypes.array.isRequired,
	allProgramGroups: PropTypes.array.isRequired,
	activeProgramGroups: PropTypes.array.isRequired,
	allPrograms: PropTypes.array.isRequired,
	account: PropTypes.object.isRequired,
	programGroup: PropTypes.object.isRequired,
	program: PropTypes.object.isRequired,
	children: PropTypes.any,
};

function useAllAccountsLoader(refreshTime, onLoad) {
	return useLoadingContext(
		() => loadAccounts(false)
			.then(allAccounts => {
				onLoad(allAccounts);
				return allAccounts;
			}),
		[refreshTime]
	);
}

function useProgramGroupsLoader(accountId, activeOnly, refreshTime, onLoad) {
	return useLoadingContext(
		() => loadProgramGroupsByAccount(accountId, activeOnly)
			.then(allProgramGroups => {
				onLoad(allProgramGroups);
				return allProgramGroups;
			}),
		[refreshTime, accountId],
		{enabled: !!accountId && accountId > 0}
	);
}

function useAllProgramsLoader(programGroupId, refreshTime, onLoad) {
	return useLoadingContext(
		() => loadProgramsByProgramGroup(programGroupId, false)
			.then(allPrograms => {
				onLoad(allPrograms);
				return allPrograms;
			}),
		[refreshTime, programGroupId],
		{enabled: !!programGroupId && programGroupId > 0}
	);
}

function useAccountLoader(accountId, refreshTime) {
	return useLoadingContext(
		() => loadAccount(accountId),
		[refreshTime, accountId],
		{enabled: !!accountId && accountId > 0}
	);
}

function useProgramGroupLoader(programGroupId, refreshTime) {
	return useLoadingContext(
		() => loadProgramGroup(programGroupId),
		[refreshTime, programGroupId],
		{enabled: !!programGroupId && programGroupId > 0}
	);
}

function useProgramLoader(programId, refreshTime) {
	return useLoadingContext(
		() => loadProgram(programId),
		[refreshTime, programId],
		{enabled: !!programId && programId > 0}
	);
}
