import Typography from "@material-ui/core/Typography";
import SelectField from "../UI/Form/Select";
import makeStyles from "@material-ui/core/styles/makeStyles";
import Box from "@material-ui/core/Box";
import cn from "classnames";
import React from "react";
import {useNavigationData} from "../Shared/Providers/NavigationDataProvider";
import CheckboxField from "../UI/Form/CheckboxField";
import TextField from "../UI/Form/TextField";
import DateField from "../UI/Form/DateField";
import {useLoadingContext} from "../Shared/LoaderUtil";
import {loadReportQuestionAutocompleteSuggestions, loadReportQuestionDetail} from "../Shared/Loaders/ReportLoaders";
import Grid from "@material-ui/core/Grid";
import BaseSwitch from "../UI/Switch/BaseSwitch";

export function Question(props) {
	const {canManageAccount, question} = props;
	const {type, selectItems} = question;
	switch(type) {
		case "ACCOUNT":
		case "ACCOUNT_AS_PERMITTED":
			return (
				<AccountQuestion {...props} permissionWarning={!canManageAccount && type === "ACCOUNT_AS_PERMITTED"}/>
			);
		case "DATE":
		case "ENDDATE":
			return (
				<DateQuestion {...props}/>
			);
		case "PROGRAMGROUP_WITH_WEBLINK":
			return (
				<ProgramGroupQuestion {...props}/>
			);
		case "PROGRAM":
		case "PROGRAM_T2C":
		case "PROGRAM_T4I":
		case "PROGRAM_WITH_SURVEY":
		case "PROGRAM_WITH_NODE_SURVEY":
			return (
				<ProgramQuestion {...props}/>
			);
		case "ADDRESS":
		case "STRING_FROM_OPTIONS":
			if(selectItems && selectItems.length > 0) {
				return (
					<SelectItemQuestion {...props}/>
				);
			} else {
				return (
					<StringItemQuestion {...props}/>
				);
			}
		case "SEARCH_CRITERIA_AUTO_FIND":
			return (
				<AutocompleteStringItemQuestion {...props}/>
			);
		case "ALLOTMENT_TYPE":
		case "BILLING_CATEGORY":
		case "BILLING_CATEGORY_OR_ANY_OR_NONE":
		case "MEDIUM":
			return (
				<SelectItemQuestion {...props}/>
			);
		case "NODE_WITH_KEYWORD":
		case "NODE_SURVEY":
		case "SURVEY":
			return (
				<SelectItemQuestion
					noneLabel={'-- ALL Campaigns --'}
					caption={'Note: If you want to select a different campaign, choose a different program.'}
					{...props}
				/>
			);

		case "VOTING_CAMPAIGN":
			return (
				<SelectItemQuestion
					noneLabel={'-- ALL Voting Campaigns --'}
					caption={'Note: If you want to select a different voting campaign, choose a different program.'}
					{...props}
				/>
			);
		case "VOTING_OPTION":
			return (
				<SelectItemQuestion
					noneLabel={'-- ALL Voting Options --'}
					{...props}
				/>
			);
		case "PROGRAM_WITH_WEBLINK":
			return (
				<SelectItemQuestion
					noneLabel={'-- ALL Programs --'}
					caption={'Note: If you want to select a different program, choose a different program group.'}
					{...props}
				/>
			);
		case "YEARMONTH":
			return (
				<YearMonthQuestion {...props}/>
			);
		case "BOOLEAN":
			return (
				<BooleanQuestion {...props}/>
			);
		default:
			return (
				<Typography gutterBottom color="error">
					<strong>Warning:</strong> Unhandled question type: "{type}"
				</Typography>
			);
	}
}

function DateQuestion(props) {
	const {question, reportParams, setReportParam} = props;
	// The expected pattern on the backend is MM-dd-yyyy
	return (
		<DateField
			label={question.label}
			info={question.description}
			value={reportParams[question.key + "-date"]}
			onChange={e => setReportParam(question.key + "-date", e.target.value)}
			optional={!question.required}
		/>
	);
}

function StringItemQuestion(props) {
	const {question, reportParams, setReportParam} = props;
	return (
		<TextField
			label={question.label}
			info={question.description}
			value={reportParams[question.key]}
			onChange={e => setReportParam(question.key, e.target.value)}
			optional={!question.required}
		/>
	);
}

function BooleanQuestion(props) {
	const {question, reportParams, setReportParam} = props;
	const valueLabel = question.selectItems.length > 0 ? question.selectItems[0].label : "Yes/No";
	return (
		<CheckboxField
			label={question.label}
			valueLabel={valueLabel}
			info={question.description}
			checked={("" + reportParams[question.key]).toLowerCase() === "true"}
			onChange={e => setReportParam(question.key, e.target.checked ? "true" : "false")}
		/>
	);
}

function AutocompleteStringItemQuestion(props) {
	const {report, question, reportParams, setReportParam} = props;

	const reportName = report.name;
	const questionKey = question.key;

	// Note: report automatching criteria supports exactly one criteria, so index is zero
	const criteriaValueKey = question.key + "[0].value";
	const criteriaValue = reportParams[criteriaValueKey];
	const criteriaExactMatchKey = question.key + "[0].exactMatch";

	// init exactMatch
	React.useEffect(() => {
		setReportParam(criteriaExactMatchKey, "false");
	}, [criteriaExactMatchKey, setReportParam]);

	const [autocompleteCtx] = useLoadingContext(
		() => loadReportQuestionAutocompleteSuggestions(reportName, questionKey, criteriaValue, reportParams),
		[reportName, questionKey, criteriaValue, JSON.stringify(reportParams)],
		{enabled: !!criteriaValue}
	);

	// TODO: implement an autocomplete UI.  Here are the options fetched from the server.
	if(autocompleteCtx.result) {
		console.log('Autocomplete options', autocompleteCtx.result);
	}

	return (
		<TextField
			label={question.label}
			info={question.description}
			value={criteriaValue}
			onChange={e => setReportParam(criteriaValueKey, e.target.value)}
			optional={!question.required}
			customLeadingContent={({fieldEnabled}) => (
				<Box mx={1} my={2}>
					<Grid container alignItems="center" spacing={1}>
						<Grid item>Contains</Grid>
						<Grid item>
							<BaseSwitch
								color="primary"
								disabled={!fieldEnabled}
								checked={("" + reportParams[criteriaExactMatchKey]).toLowerCase() === "true"}
								onChange={e => setReportParam(criteriaExactMatchKey, e.target.checked ? "true" : "false")}
							/>
						</Grid>
						<Grid item>Equals</Grid>
					</Grid>
				</Box>
			)}
		/>
	);
}

function SelectItemQuestion(props) {
	const {report, question, reportParams, setReportParam, noneLabel = "None", caption} = props;

	const {account} = useNavigationData();
	const accountId = account && account.id ? account.id : null;

	const isRequired = !!question.required;
	const isDependent = !!question.dependencyKey;
	const reportName = report.name;
	const questionKey = question.key;
	const dependencyValue = question.dependencyKey ? reportParams[question.dependencyKey] : null;

	const [questionDetailCtx] = useLoadingContext(
		() => loadReportQuestionDetail(reportName, questionKey, dependencyValue, accountId),
		[reportName, questionKey, dependencyValue, accountId],
		{enabled: (dependencyValue && accountId)}
	);

	// set first value as selection when loading new answer choices if question is required
	React.useEffect(() => {
		if(isDependent) {
			if (questionDetailCtx.loading) {
				setReportParam(question.key, "");
			} else {
				if (questionDetailCtx.result && isRequired && questionDetailCtx.result.selectItems && questionDetailCtx.result.selectItems.length > 0) {
					setReportParam(question.key, questionDetailCtx.result.selectItems[0].value);
				}
			}
		}
	}, [isRequired, isDependent, questionDetailCtx.loading, questionDetailCtx.result, question.key]);

	const selectItems = (isDependent && questionDetailCtx.result ? questionDetailCtx.result.selectItems : question.selectItems) || [];
	const effectiveItems = question.required ? selectItems : [{label: noneLabel, value: ""}, ...selectItems];
	const hasSelectItems = effectiveItems && effectiveItems.length > 0;

	const loading = isDependent && questionDetailCtx.loading;
	const error = isDependent && questionDetailCtx.reason;
	const disabled = !!(!hasSelectItems || loading || error);

	let options = effectiveItems;
	let value = reportParams[question.key];
	let onChange = e => setReportParam(question.key, e.target.value);

	if(isDependent && questionDetailCtx.loading) {
		options = [{label: "Please wait...", value: ""}];
		value = "";
		onChange = () => {};
	} else if (isDependent && questionDetailCtx.reason) {
		options = [{label: "Error: Could not load options", value: ""}];
		value = "";
		onChange = () => {};
	} else {
		if(effectiveItems.length < 1) {
			options = [{label: "Error: No options available", value: ""}];
			value = "";
		}
	}

	return (
		<>
			<SelectField
				label={question.label}
				info={question.description}
				options={options}
				disabled={disabled}
				value={value}
				onChange={onChange}
				displayEmpty
			/>
			{caption && (
				<Typography variant="caption">
					{caption}
				</Typography>
			)}
		</>
	);
}

function AccountQuestion(props) {
	const {question, reportParams, setReportParam, permissionWarning} = props;
	const {account} = useNavigationData();

	const [checked, setChecked] = React.useState(() => question.required || reportParams[question.key] === account.id.toString());

	React.useEffect(() => {
		if (question) {
			if (checked) {
				setReportParam(question.key, account.id);
			} else {
				setReportParam(question.key);
			}
		}
	}, [question, account, checked]);

	return (
		<>
			{!question.required ? (
				<CheckboxField
					label={question.label}
					valueLabel={account.label}
					info={question.description}
					checked={checked}
					onChange={e => setChecked(e.target.checked)}
				/>
			) : (
				<TextField
					disabled
					label={question.label}
					value={account.label}
					info={question.description}
				/>
			)}

			<Typography variant="caption">
				Note: If you want to select a different account, choose a different account from the top navigation.
			</Typography>
			{permissionWarning && (
				<Typography variant="caption" component="div" color="error">
					Warning: You are missing a permission on this account. The report may not be able to run.
				</Typography>
			)}
		</>
	);
}

function ProgramGroupQuestion(props) {
	return (
		<SelectItemQuestion
			caption="If you want to select a different program group, choose a different account from the top navigation."
			{...props}
		/>
	);
}
function ProgramQuestion(props) {
	return (
		<SelectItemQuestion
			caption="If you want to select a different program, choose a different account from the top navigation."
			{...props}
		/>
	);
}

const useYearMonthQuestionStyles = makeStyles(theme => ({
	dateBox: {
		display: 'flex',
		alignItems: 'flex-end',
		'& .form-group': {
			marginRight: theme.spacing(1)
		}
	},
}), {name: 'YearMonthQuestion'});

const months = Object.freeze(
	"1:January,2:February,3:March,4:April,5:May,6:June,7:July,8:August,9:September,10:October,11:November,12:December"
		.split(",")
		.map(mStr => mStr.split(":"))
		.map(mArr => ({label: mArr[0] + " - " + mArr[1], value: mArr[0]}))
		.map(Object.freeze)
);

function genYears() {
	const years = [];
	const thisYear = new Date().getFullYear();
	for(let y = thisYear - 5; y <= thisYear + 5; y++) {
		const ystr = y.toString();
		years.push({label: ystr, value: ystr});
	}
	return years;
}

function YearMonthQuestion(props) {
	const {question, reportParams, setReportParam} = props;
	const classes = useYearMonthQuestionStyles();
	const years = genYears();

	// Note: currently the server will never specify a YEARMONTH question that is not required...

	return (
		<Box className={cn(classes.dateBox)}>
			<SelectField
				label={question.label}
				info={question.description}
				options={months}
				value={reportParams[question.key + "-month"]}
				onChange={setReportParam ? e => setReportParam(question.key + "-month", e.target.value) : () => {}}
			/>
			<SelectField
				options={years}
				value={reportParams[question.key + "-year"]}
				onChange={setReportParam ? e => setReportParam(question.key + "-year", e.target.value) : () => {}}
			/>
		</Box>
	);
}
