import sa from "superagent";

/**
 * @param {number} programId
 * @returns {Promise<[]>} a promise resolved to an array of message templates
 */
export function loadWeblinks(programId) {
    return sa.get('/rest/v1/programs/' + programId + '/weblinks')

        .then((r) => {
            return r.body});
}

/**
 * @param {number} programId
 * @param {number} linkId
 * @param {string} unit
 * @param {number} units
 * @returns {Promise<[]>} a promise resolved to an array of message templates
 */
export function loadLinkReport(programId, linkId, unit = 'day', units = 30) {
    const reportType = linkId ? `programWeblinkId=${linkId}` : 'fullReport=true';
    return sa.get(
        `/rest/v1/programs/${programId}/weblinks/linkReport?${reportType}&unit=${unit}&units=${units}`
    ).then((r) => {
        return r.body
    });
}

