import React from "react";
import PropTypes from "prop-types";
import cn from 'classnames';
import makeStyles from "@material-ui/core/styles/makeStyles";

const useStyledImageStyles = makeStyles(theme => ({
	imgRounded: {
		borderRadius: theme.shape.borderRadius,
	},
	imgResponsive: {
		display: "block",
		maxWidth: "100%",
		height: "auto",
	},
}), {name: "StyledImage"});

export default function StyledImage(props) {
	let {src, alt, rounded, responsive, className} = props;

	const classes = useStyledImageStyles();
	return (
		<img
			src={src}
			alt={alt}
			className={cn(className, {[classes.imgRounded]: rounded}, {[classes.imgResponsive]: responsive})}
		/>
	);
}
StyledImage.propTypes = {
	src: PropTypes.string.isRequired,
	alt: PropTypes.string.isRequired,
	rounded: PropTypes.bool,
	responsive: PropTypes.bool,
	className: PropTypes.string,
};
