import sa from "superagent";

/**
 * @param programId {number}
 * @return {Promise<[]>} a promise resolved to the program
 */
export function loadProgram(programId) {
	return sa.get('/rest/v1/programs/' + programId)
		.then(r => r.body);
}

/**
 * @param accountId {number}
 * @param activeOnly {boolean}
 * @return {Promise<[]>}
 */
export function loadProgramsByAccount(accountId, activeOnly) {
	return sa.get('/rest/v1/accounts/' + accountId + '/programs')
		.query({
			"$activeOnly": activeOnly,
		})
		.then(r => r.body);
}

/**
 * @param programGroupId {number}
 * @param activeOnly {boolean} false to include inactive programs
 * @return {Promise<T>}
 */
export function loadProgramsByProgramGroup(programGroupId, activeOnly) {
	return sa.get('/rest/v1/programGroups/' + programGroupId + '/programs')
		.query({
			"$activeOnly": activeOnly,
		})
		.then(r => r.body);
}

/**
 * @param accountId {number}
 * @param keys {string[]}
 * @returns {Promise<[]>} a promise resolved to program data values on the account
 */
export function loadProgramDataByAccountAndKeys(accountId, keys) {
	return sa.get('/rest/v1/accounts/' + accountId + '/programDatas')
		.query({
			"$query": "allDataByKeys",
			key: keys,
		})
		.then(r => r.body);
}

/**
 * @param programId {number}
 * @param keys {string[]}
 * @returns {Promise<[]>} a promise resolved to program data values
 */
export function loadProgramDataByProgramAndKeys(programId, keys) {
	return sa.get('/rest/v1/programs/' + programId + '/programDatas')
		.query({
			"$query": "allDataByKeys",
			key: keys,
		})
		.then(r => r.body);
}

/**
 * @param accountId {number}
 * @param key {string}
 * @returns {Promise<[]>} a promise resolved to the distinct program data values
 */
export function loadUniqueProgramDataValuesByAccountAndKey(accountId, key) {
	return sa.get('/rest/v1/accounts/' + accountId + '/programDatas')
		.query({
			"$query": "valuesByKey",
			key: key,
		})
		.then(r => r.body);
}
