import React from 'react'
import {makeStyles} from "@material-ui/core/styles";
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';

const useMaterialModalStyles = makeStyles(theme => ({
	modal: {
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		'& .delete-modal-content':{
			width: 370,
			padding: '40px 60px',
			borderRadius: 15,
			backgroundColor: theme.palette.additional.white,
			borderTop: `5px solid ${theme.palette.error.main}`,
			'& .text': {
				textAlign: 'center',
			},
			'& .actions':{
				marginTop: theme.spacing(6),
				display: 'flex',
				'& button': {
					flex: 1,
				},
				'& button:first-of-type': {
					marginRight: theme.spacing(2)
				}
			}
		},
		'& .modal-content': {
			width: '50%',
			// maxWidth: 800,
			maxHeight: '90%',
			overflow: 'auto',
			padding: theme.spacing(6),
			borderRadius: 15,
			backgroundColor: theme.palette.additional.white,
		}
	},
	backDrop: {
		backgroundColor: 'rgba(60, 63, 84, 0.4)',
	}
}), {name: "MaterialModal"});

export default function MaterialModal({children, open = false, onClose}) {
	const classes = useMaterialModalStyles();

	return (
		<Modal
			aria-labelledby="transition-modal-title"
			aria-describedby="transition-modal-description"
			onClose={onClose}
			className={classes.modal}
			open={open}
			BackdropComponent={Backdrop}
			BackdropProps={{
				timeout: 500,
				className: classes.backDrop,
				opacity: 0.4
			}}
		>
			<Fade in={open}>
				{children}
			</Fade>
		</Modal>
	);
}
