import React from "react";
import PropTypes from "prop-types";
import cn from 'classnames';
import Twemoji from "../Shared/Twemoji";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {LocalizedDateTime} from "./LocalizedDateTime";
import Alert from "@material-ui/lab/Alert";
import Typography from "@material-ui/core/Typography";
import {fade} from "@material-ui/core/styles";
import BaseButton from "../UI/Buttons/BaseButton";
import {ReactComponent as FileIcon} from "../../assets/svg/file.svg"
import StyledImage from "./StyledImage";

const useMessagesDisplayStyles = makeStyles(theme => ({
	senderImgContainer: {
		position: "relative",
	},
	senderImgPositioner: {
		position: "absolute",
		bottom: "0",
		right: "0",
		paddingLeft: "unset",
		width: "54.19px",
	},
	miriImg: {
		width: "35px"
	},
	userImg: {
		width: "36px"
	},
}), {name: "MessagesDisplay"});

export function MessagesDisplay(props) {
	let {messages, renderMessageText, renderMessageSupplement, onLinkClick, hideMetadata} = props;
	const classes = useMessagesDisplayStyles();

	if (!renderMessageText) {
		renderMessageText = (text) => (<MessageText text={text} onLinkClick={onLinkClick}/>);
	}

	return (
		<>
			{messages.map((msg, index) => {
				let {inbound, senderName} = msg;
				return (
					<div key={index}>
						<SingleMessage
							msg={msg}
							renderMessageText={renderMessageText}
							renderMessageSupplement={renderMessageSupplement}
							hideMetadata={hideMetadata}
						/>
						{!inbound && senderName && (
							<div className={classes.senderImgContainer}>
								<div className={classes.senderImgPositioner}>
{/*TODO: These images will overlap the actions, if any*/}
{/*TODO these images don't exist in Indigo*/}
									{senderName === "chatbot" ? (
										<img className={classes.miriImg} src={"/static/images/chatIcons/MIRI_Icon.png"} alt="MIRI"/>
									) : (
										<img className={classes.userImg} src={"/static/images/chatIcons/User_Icon.png"} alt="User"/>
									)}
								</div>
							</div>
						)}
					</div>
				);
			})}
		</>
	);
}
MessagesDisplay.propTypes = {
	messages: PropTypes.arrayOf(PropTypes.shape({
		id: PropTypes.number,
		programId: PropTypes.number,
		text: PropTypes.string,
		/** true if it's an inbound message */
		inbound: PropTypes.bool,
		hidden: PropTypes.bool,
		/** One way of providing a message attachment as an object */
		attachment: PropTypes.shape({
			/** The content type of the file, such as "image/jpeg" */
			contentType: PropTypes.string.isRequired,
			/** The local URI of the file, such as "/rest/v1/nodes/82/surveys/questions/17/attachments/22" */
			localUri: PropTypes.string.isRequired,
		}),
		/** One way of providing message attachments as an array of file IDs */
		attachmentFileIds: PropTypes.array,
		/** original schedule date as an ISO date */
		originalScheduleDate: PropTypes.string,
		/** transit date as an ISO date */
		transitDate: PropTypes.string,
		senderName: PropTypes.string,
	})).isRequired,
	/** Called with params (text:string) to render custom content for the non-empty message text */
	renderMessageText: PropTypes.func,
	/** Called with params (msg:object) to render supplemental content for a message */
	renderMessageSupplement: PropTypes.func,
	/** Called with params (text:string) when some text that looks like a URL is clicked. This is only used if "renderMessageText" is not provided. */
	onLinkClick: PropTypes.func,
	hideMetadata: PropTypes.bool,
};

const useSingleMessageStyles = makeStyles(theme => ({
	msgContainer: {
		display: 'flex',
		flexDirection: 'column',
	},
	contentWrapper: {
		maxWidth: '90%',
		marginTop: theme.spacing(2),
	},
	inboundContent: {
		alignSelf: 'flex-start',
		"& $messageBubble": {
			padding: theme.spacing(1),
			backgroundColor: fade(theme.palette.secondary.main, 0.06),
		},
	},
	outboundContent: {
		alignSelf: 'flex-end',
		"& $messageBubble": {
			padding: theme.spacing(2),
			backgroundColor: theme.palette.primary.main,
			color: theme.palette.getContrastText(theme.palette.primary.main),
		},
	},
	messageBubble: {
		wordWrap: 'break-word',
		whiteSpace: 'pre-wrap',
		borderRadius: theme.shape.borderRadius,
	},
}), {name: "SingleMessage"});

function SingleMessage(props) {
	let {msg, renderMessageText, renderMessageSupplement, hideMetadata} = props;
	let {id, programId, text, inbound, hidden, attachment, attachmentFileIds} = msg;

	const classes = useSingleMessageStyles();
	return (
		<div className={cn(classes.msgContainer)}>
			<div className={cn(classes.contentWrapper, inbound ? classes.inboundContent : classes.outboundContent)}>
				{attachmentFileIds ? (
					attachmentFileIds.map((fileId, idx) => {
						let content = {
							contentType: "unknown",
							localUri: "/rest/v1/programs/" + (programId || -1) + "/messages/" + (id || -1) + "/attachments/" + fileId,
						};
						return (<MessageAttachment key={idx} content={content} inbound={inbound}/>);
					})
				) : (
					<MessageAttachment content={attachment} inbound={inbound}/>
				)}

				{!hideMetadata && (
					<MessageMetadata
						message={msg}
					/>
				)}

				{text && text.length > 0 && (
					<div className={classes.messageBubble}>
						{hidden ? (
							<Alert severity="warning">[Hidden content]</Alert>
						) : (
							renderMessageText(text)
						)}
						{renderMessageSupplement && renderMessageSupplement(msg)}
					</div>
				)}
			</div>
		</div>
	);
}
SingleMessage.propTypes = {
	msg: PropTypes.shape({
		id: PropTypes.number,
		programId: PropTypes.number,
		text: PropTypes.string,
		/** true if it's an inbound message */
		inbound: PropTypes.bool,
		hidden: PropTypes.bool,
		/** One way of providing a message attachment as an object */
		attachment: PropTypes.shape({
			contentType: PropTypes.string.isRequired,
			localUri: PropTypes.string.isRequired,
		}),
		/** One way of providing message attachments as an array of file IDs */
		attachmentFileIds: PropTypes.array,
		/** original schedule date as an ISO date */
		originalScheduleDate: PropTypes.string,
		/** transit date as an ISO date */
		transitDate: PropTypes.string,
		senderName: PropTypes.string,
	}).isRequired,
	/** Called with params (text:string) to render custom content for the non-empty message text */
	renderMessageText: PropTypes.func.isRequired,
	/** Called with params (msg:object) to render supplemental content for a message */
	renderMessageSupplement: PropTypes.func,
	hideMetadata: PropTypes.bool,
};

const useMessageMetadataStyles = makeStyles(theme => ({
	msgMetadata: {
		color: theme.palette.text.secondary,
		fontStyle: "italic",
	},
}), {name: "MessageMetadata"});

export function MessageMetadata(props) {
	const {message} = props;

	const classes = useMessageMetadataStyles();

	const sent = !!message.transitDate;
	const date = sent ? message.transitDate : message.originalScheduleDate;
	const sender = message.senderName ? " By ".concat(message.senderName) : "";
	return (
		<div className={classes.msgMetadata}>
			{!sent && (<Typography variant="body2" color="error">Not yet sent.</Typography>)}
			{!!date && (
				<span>
					{!sent && (' Scheduled for ')}
					<LocalizedDateTime isoDate={date} noZone noSeconds/>
				</span>
			)}
			<span>{sender}</span>
		</div>
	);
}
MessageMetadata.propTypes = {
	message: PropTypes.shape({
		/** original schedule date as an ISO date */
		originalScheduleDate: PropTypes.string,
		/** transit date as an ISO date */
		transitDate: PropTypes.string,
		senderName: PropTypes.string,
	}).isRequired,
};

const useMessageAttachmentStyles = makeStyles(theme => ({
	attachment: {
		// marginBottom: theme.spacing(2),
		padding: [[theme.spacing(2), 0]],
	},
	outboundAttachment: {
		textAlign: "right",
	},
	attachmentImg: {
		backgroundColor: "transparent",
	},
}), {name: "MessageAttachment"});

export function MessageAttachment(props) {
	let {content, inbound} = props;

	const classes = useMessageAttachmentStyles();

	if (!content) {
		return null;
	}
	let {contentType, localUri} = content;

	if (contentType === "unknown" || !contentType) {
		let req = new XMLHttpRequest();
		req.open('GET', localUri, false);
		req.send(null);
		contentType = req.getResponseHeader("Content-Type").toLowerCase();
	}

	let isImage = contentType.indexOf("image/") === 0;
	if (isImage) {
		return (
			<div className={classes.attachment}>
				<StyledImage
					src={localUri}
					alt="Attachment"
					rounded
					responsive
					className={classes.attachmentImg}
				/>
			</div>
		);
	} else {
		let isAudio = contentType.indexOf("audio/") === 0;
		let isVideo = contentType.indexOf("video/") === 0;
		let label = isAudio ? "Audio File" : (isVideo ? "Video File" : "File (type unknown)");
		// TODO alternate icons if "isAudio" or "isVideo"
		let icon = (<FileIcon/>);

		return (
			<div className={cn(classes.attachment, {[classes.outboundAttachment]: !inbound})}>
				<BaseButton
					component="a"
					href={localUri}
					target="_blank"
					size="small"
					type="secondary"
					startIcon={icon}
				>
					{label}
				</BaseButton>
			</div>
		);
	}
}
MessageAttachment.propTypes = {
	content: PropTypes.shape({
		contentType: PropTypes.string.isRequired,
		localUri: PropTypes.string.isRequired,
	}),
	/** true if it's an inbound message */
	inbound: PropTypes.bool,
};

export function MessageText(props) {
	let {text, onLinkClick} = props;
	const urlRegex = /(https?:\/\/[^\s]+)/g;
	if(!!onLinkClick) {
		return (
			<div>
				{text.split(urlRegex).map((str, index) => {
					if (str.match(urlRegex)) {
						return <a key={index} href={str} target="_blank" onClick={() => {
							onLinkClick(str);
						}}>{str}</a>;
					} else {
						return (<Twemoji text={str}/>);
					}
				})}
			</div>
		);
	} else {
		return (<Twemoji text={text}/>);
	}
}
MessageText.propTypes = {
	text: PropTypes.string.isRequired,
	/** Called with params (text:string) when some text that looks like a URL is clicked */
	onLinkClick: PropTypes.func,
};
