import React from 'react'
import ReportingHeader from "../Reporting/Header";
import ReportingContent from "../Reporting/Content";
import {useNavigationData} from "../Shared/Providers/NavigationDataProvider";
import {useCurrentUser} from "../Shared/Providers/CurrentUserProvider";
import {hasReadAccess, useCurrentUserAccountPermission, userHasAdminPrivilege} from "../Shared/PermissionUtil";
import {
    loadExternalReports,
    loadInternalReports,
    loadReportData,
    loadReportDetail
} from "../Shared/Loaders/ReportLoaders";
import {useLoadingContext} from "../Shared/LoaderUtil";
import Typography from "@material-ui/core/Typography";
import CenterProgress from "../Shared/CenterProgress";
import {useClassicState} from "../../utilities/hooks";
import {todayMDY} from "../../utilities/dates";

export default function ReportingWebLinks (props) {
    const {account, program} = useNavigationData();
    const user = useCurrentUser();
    const isAdmin = userHasAdminPrivilege(user, 'SYSTEM_ADMIN');

    const [uapCtx] = useCurrentUserAccountPermission(account ? account.id : null);
    const canManageAccount = hasReadAccess(uapCtx.result);

    function buildDefaultReportParams(report) {
        const newParams = {};

        if(report && report.questions) {
            report.questions.filter(q => q.required).forEach(q => {
                // TODO: try to read from the URL params

                if (q.selectItems && q.selectItems.length > 0) {
                    // some types have select items populated from the server

                    function pickTheFirstOne() {
                        newParams[q.key] = q.selectItems[0].value;
                    }
                    switch (q.type) {
                        case "PROGRAM":
                        case "PROGRAM_T2C":
                        case "PROGRAM_T4I":
                        case "PROGRAM_WITH_NODE_SURVEY":
                            // try current program
                            const programId = program.id.toString();
                            const item = q.selectItems.find(si => si.value === programId);
                            if(item) {
                                newParams[q.key] = item.value;
                            } else {
                                pickTheFirstOne();
                            }
                            break;
                        case "BOOLEAN": // BOOLEAN has selectItems in order to provide the label...
                            newParams[q.key] = "false";
                            break;
                        default:
                            // if nothing else, just pick the first one
                            pickTheFirstOne();
                            break;
                    }

                } else {
                    // some types do not have select items populated from the server
                    switch (q.type) {
                        case "ACCOUNT":
                        case "ACCOUNT_AS_PERMITTED":
                            newParams[q.key] = account.id.toString();
                            break;
                        case "DATE":
                        case "ENDDATE":
                            newParams[q.key + "-date"] = todayMDY();
                            break;
                        case "YEARMONTH":
                            const thismonth = new Date();
                            newParams[q.key + "-month"] = (thismonth.getMonth() + 1).toString();
                            newParams[q.key + "-year"] = thismonth.getFullYear().toString();
                            break;
                        default:
                            break;
                    }
                }
            });
        }

        return newParams;
    }

    const accountId = account && account.id ? account.id : null;

    const [showInternalReports, setShowInternalReports] = React.useState(false);
    const [selectedReportName, setSelectedReportName] = React.useState(null);
    const [reportParams, setReportParams, replaceReportParams] = useClassicState(null);

    const [externalReportsCtx, setExternalReportsCtx] = useLoadingContext(
        () => loadExternalReports(accountId),
        [accountId],
        {enabled: !!accountId}
    );
    const [internalReportsCtx, setInternalReportsCtx] = useLoadingContext(
        () => loadInternalReports(accountId),
        [isAdmin, accountId],
        {enabled: isAdmin && !!accountId}
    );
    const [selectedReportDetailCtx, setSelectedReportDetailCtx] = useLoadingContext(
        () => loadReportDetail(selectedReportName, accountId),
        [accountId, selectedReportName],
        {enabled: !!accountId && selectedReportName}
    );

    const setReportParam = React.useCallback((key, value) => {
        setReportParams({[key]: value});
    }, []);
    const switchReportName = React.useCallback(newReportName => {
        replaceReportParams(null);
        setSelectedReportName(newReportName);
    }, []);

    // Initialize params when report changes
    React.useEffect(() => {
        if(selectedReportDetailCtx.result) {
            replaceReportParams(buildDefaultReportParams(selectedReportDetailCtx.result));
        }
    }, [selectedReportDetailCtx.result]);

    const reports = React.useMemo(() => {
        if(!showInternalReports) {
            return externalReportsCtx.result || [];
        } else {
            return [...(externalReportsCtx.result || []), ...(internalReportsCtx.result || [])]
                .sort((a, b) => a.label.localeCompare(b.label, 'en', { sensitivity: 'base' }))
        }
    }, [showInternalReports, externalReportsCtx.result, internalReportsCtx.result]);

    const ready = !!accountId && !!user && !externalReportsCtx.loading && !internalReportsCtx.loading;
    const error = externalReportsCtx.reason || internalReportsCtx.reason || selectedReportDetailCtx.reason;

    const selectedReportDetail = selectedReportDetailCtx.result && selectedReportDetailCtx.result.name === selectedReportName ? selectedReportDetailCtx.result : null;

    return !!error ? (
        <Typography color="error">There was an error while loading report data.</Typography>
    ) : (
        <>
            <pre>reportParams:{JSON.stringify(reportParams, null, 2)}</pre>
            <ReportingHeader
                allowInternal={isAdmin}
                showInternal={showInternalReports}
                setShowInternal={setShowInternalReports}
            />
            {!ready ? (
                <CenterProgress/>
            ) : (
                <ReportingContent
                    canManageAccount={canManageAccount}
                    reports={reports}
                    selectedReportName={selectedReportName}
                    setSelectedReportName={switchReportName}
                    selectedReportDetail={selectedReportDetail}
                    reportParams={reportParams}
                    setReportParam={setReportParam}
                    runReport={loadReportData}
                    {...props}
                />
            )}
        </>
    )
}
