import React from 'react'
import PropTypes from 'prop-types';
import PageContainer from "../PageContainer";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import {Link} from "react-router-dom";
import Box from "@material-ui/core/Box";

export default function HomePage() {

// TODO Header shouldn't have to be told the "activeMenuItem"

	return (
		<PageContainer pageTitle="Home">
			<Typography variant="h1" gutterBottom>
				Hello
			</Typography>
			<Typography paragraph>
				Welcome to Indigo.
			</Typography>
		</PageContainer>
	);
}

function TempHomeLink(props) {
	let {to, children} = props;
	return (
		<Box mb={2}>
			<Button component={Link} to={to} variant="contained" color="primary">
				{children}
			</Button>
		</Box>
	);
}
TempHomeLink.propTypes = {
	/** The react-router link location */
	to: PropTypes.oneOfType([PropTypes.string, PropTypes.object, PropTypes.func]).isRequired,
	/** The button content */
	children: PropTypes.any.isRequired,
};
