import {useLoadingContext} from "./LoaderUtil";
import {loadUserAccountPermission, loadUserAccountPermissionByProgram, loadUserProgramPermission} from "./Loaders/UserLoaders";
import {useCurrentUser} from "./Providers/CurrentUserProvider";

export function useCurrentUserAccountPermission(accountId) {
	const currentUser = useCurrentUser();
	return useUserAccountPermissionLoader(accountId, currentUser ? currentUser.id : null);
}

export function useUserAccountPermissionLoader(accountId, userId) {
	return useLoadingContext(
		() => loadUserAccountPermission(accountId, userId)
			// If the request fails, assume it's because they don't have an account permission
			.catch(() => null),
		[accountId, userId],
		{enabled: !!accountId && accountId > 0 && !!userId && userId > 0}
	);
}

export function useCurrentUserAccountPermissionByProgram(programId) {
	const currentUser = useCurrentUser();
	return useUserAccountPermissionLoaderByProgram(programId, currentUser ? currentUser.id : null);
}

export function useUserAccountPermissionLoaderByProgram(programId, userId) {
	return useLoadingContext(
		() => loadUserAccountPermissionByProgram(programId, userId)
			// If the request fails, assume it's because they don't have an account permission
			.catch(() => null),
		[programId, userId],
		{enabled: !!programId && programId > 0 && !!userId && userId > 0}
	);
}

export function useCurrentUserProgramPermission(programId) {
	const currentUser = useCurrentUser();
	return useUserProgramPermissionLoader(programId, currentUser ? currentUser.id : null);
}

export function useUserProgramPermissionLoader(programId, userId) {
	return useLoadingContext(
		() => loadUserProgramPermission(programId, userId)
			// If the request fails, assume it's because they don't have a program permission
			.catch(() => null),
		[programId, userId],
		{enabled: !!programId && programId > 0 && !!userId && userId > 0}
	);
}

/**
 * A centralized function to ensure consistent logic when checking READ permission access
 * @return {boolean} true if the permission type indicates the user has READ access
 */
export function hasReadAccess(userPermission) {
	let {permissionType} = userPermission || {};
	return !!permissionType;
}

/**
 * A centralized function to ensure consistent logic when checking WRITE permission access (or higher)
 * @return {boolean} true if the permission type indicates the user has WRITE access (or higher)
 */
export function hasWriteAccess(userPermission) {
	let {permissionType} = userPermission || {};
	return !!permissionType && permissionType !== "READ";
}

/**
 * A centralized function to ensure consistent logic when checking ADMIN permission access
 * @return {boolean} true if the permission type indicates the user has ADMIN access
 */
export function hasAdminAccess(userPermission) {
	let {permissionType} = userPermission || {};
	return permissionType === "ADMIN";
}

/**
 * @param adminPrivilegeId {string} the admin privilege ID, such as "PROGRAM_ALL_READ"
 * @return {boolean} true if the current user has the internal admin privilege
 */
export function useHasAdminPrivilege(adminPrivilegeId) {
	const currUser = useCurrentUser();
	return userHasAdminPrivilege(currUser, adminPrivilegeId);
}

/**
 * @param user {object|null} the user record with any admin role and admin privileges
 * @param adminPrivilegeId {string} the admin privilege ID, such as "PROGRAM_ALL_READ"
 * @return {boolean} true if the user has the internal admin privilege
 */
export function userHasAdminPrivilege(user, adminPrivilegeId) {
	if (!user) {
		return false;
	}
	let {adminRole} = user;
	if (!adminRole) {
		return false;
	}
	let {adminPrivileges} = adminRole;
	if (!adminPrivileges) {
		return false;
	}
	return !!adminPrivileges.find(adminPriv => adminPriv.id === adminPrivilegeId);
}

/**
 * @param requiredPermissionLevel {string} the required permission level such as 'READ', 'WRITE', or 'ADMIN'
 * @param userProgramPermission {{}|null} the user program permission to check or null if there is none
 * @return {boolean} true if the user has explicit permission on a program. Global program access is not checked for this.
 */
export function hasExclusivePermission(requiredPermissionLevel, userProgramPermission) {
	if (!userProgramPermission) {
		return false;
	}
	let {permissionType} = userProgramPermission;

	let result = false;
	switch (permissionType) {
		// Based on IrioDomainObjectPermissionEvaluator.java's checkPermissionLevel()
		// Note: All fallthroughs and boolean short circuit evaluation is intended here.
		case "ADMIN":
			result = result || "WRITE" === requiredPermissionLevel;
			break;
		case "WRITE":
			result = result || "READ" === requiredPermissionLevel;
			break;
		default:
			result = result || permissionType === requiredPermissionLevel;
			break;
	}
	return result;
}

/**
 * @param requiredPermissionLevel {string} the required permission level such as 'READ', 'WRITE', or 'ADMIN'
 * @param userProgramPermission {{}|null} the user program permission to check or null if there is none
 * @return {boolean} true if the user has explicit permission on a program OR global program access as an internal administrator.
 */
export function useHasProgramPermission(requiredPermissionLevel, userProgramPermission) {
	const currUser = useCurrentUser();
	return userHasProgramPermission(currUser, requiredPermissionLevel, userProgramPermission);
}

/**
 * @param user {object|null}
 * @param requiredPermissionLevel {string} the required permission level such as 'READ', 'WRITE', or 'ADMIN'
 * @param userProgramPermission {{}|null} the user program permission to check or null if there is none
 * @return {boolean} true if the user has explicit permission on a program OR global program access as an internal administrator.
 */
export function userHasProgramPermission(user, requiredPermissionLevel, userProgramPermission) {
	let hasAdminPriv = userHasAdminPrivilege(user, 'PROGRAM_ALL_' + requiredPermissionLevel);
	let hasExclusivePerm = hasExclusivePermission(requiredPermissionLevel, userProgramPermission);
	return hasAdminPriv || hasExclusivePerm;
}
