import React from "react";
import PropTypes from "prop-types";
import {useLoadingContext} from "../LoaderUtil";
import {loadCurrentUser} from "../Loaders/UserLoaders";
import LinearProgress from "@material-ui/core/LinearProgress";

const CurrentUserContext = React.createContext(null);

/**
 * @return {{id:number, loginName:string, name:string, adminRole:{id:string, adminPrivileges:[]}|null}|null} the current user, or null if not logged in
 */
export function useCurrentUser() {
	return React.useContext(CurrentUserContext);
}

export function CurrentUserProvider(props) {
	let {authenticated, children} = props;

	const [currUserCtx] = useLoadingContext(
		() => loadCurrentUser(),
		[],
		{enabled: authenticated}
	);
	let {loading, reason, result: currentUser} = currUserCtx;

	return (
		<CurrentUserContext.Provider value={currentUser}>
			{loading ? (
				<LinearProgress/>
			) : reason ? (
				<div>Could not load your user record.</div>
			) : children}
		</CurrentUserContext.Provider>
	);
}
CurrentUserProvider.propTypes = {
	/** true if the current user is authenticated */
	authenticated: PropTypes.bool,
	children: PropTypes.any,
};

/**
 * A <CurrentUserProvider/> with mock data, such as for example components
 */
export function MockCurrentUserProvider(props) {
	let {currentUser, children} = props;

	return (
		<CurrentUserContext.Provider value={currentUser}>
			{children}
		</CurrentUserContext.Provider>
	);
}
MockCurrentUserProvider.propTypes = {
	currentUser: PropTypes.object,
	children: PropTypes.any,
};
